.pathway-image-cards {
    background-color: $color-gray2;
    padding: $measure--small4;

    h3 {
        text-align: center;
    }
}

.pathway-image-cards__blocks {
    display: grid;
    grid-column-gap: $measure--small4;
    grid-row-gap: $measure--medium7;
    margin-top: $measure--small4;

    @include media('>=tablet') {
        grid-template-columns: auto auto;
    }

    @include media('>desktop') {
        grid-template-columns: auto auto auto;
    }
}

.card--pathway-image {
    a {
        display: block;
        border: none;
        transition: none;
        color: $color-nearblack;
        position: relative;
        z-index: 1;

        &:visited {
            color: $color-nearblack;
        }

        &:hover,
        &:active,
        &:focus {
            background-image: none;
            background-color: transparent;
            border: none;
        }

        &:focus,
        &:active {
            outline: none;

            &:after {
                @include outline-properties();
            }
        }

        @include thumbnail-gradient-scale;
        @include background-color-and-scale($color-gray1, 1.1);
        @include underline-transition--title;

        img {
            transition: transform $transition-duration ease-in;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }

    h3 {
        @include betatype();
        color: $color-gray7;
    }
}

.card--pathway-image__icon {
    display: block;
    text-align: right;
    font-weight: $font-weight-normal;
}

.card--pathway-image__container {
    overflow: hidden;
    margin-bottom: $measure--basic;
}

.pathway-image-cards__blocks {
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3) {
        a {
            &:hover {
                color: $color-purewhite;
            }
        }
    }

    div:nth-child(1) {
        a {
            @include background-color-and-scale($color-purple50, 1.1);
            @include underline-transition--title(
                $color-gray7,
                $color-purewhite
            );
        }
    }

    div:nth-child(2) {
        a {
            @include background-color-and-scale($color-brown90, 1.1);
            @include underline-transition--title(
                $color-gray7,
                $color-purewhite
            );
        }
    }

    div:nth-child(3) {
        a {
            @include background-color-and-scale($color-pink40, 1.1);
            @include underline-transition--title(
                $color-gray7,
                $color-purewhite
            );
        }
    }
}
