.navigation {
    a,
    &.navigation__current > span {
        padding: $measure--quarter;
        border: none;
    }

    &.navigation__current > span:focus {
        outline-color: $color-text;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
    }

    a {
        @include anchor-menu();
    }

    &.navigation__hierarchy > a {
        span {
            border-bottom-width: $border-width-default;
            border-bottom-style: $border-style-solid;
            border-bottom-color: $color-text;
        }
    }
}

@import 'navigation--primary';
@import 'navigation--secondary';
