////
/// Theme Switcher
/// @group patterns
////
.theme-switcher {
    list-style-type: none;

    button {
        @include extrasmalltype();
    }
}

.theme-switcher__option {
    display: list-item;

    button {
        @include anchor-inline();
    }
}

.theme-switcher__option--clear {
    display: none;
}

.theme--dark {
    .theme-switcher__option--clear {
        display: list-item;
    }

    .theme-switcher__option--dark {
        display: none;
    }
}

.theme-switcher {
    display: none;
}

.js {
    .theme-switcher {
        @include media('screen') {
            display: block;
        }
    }
}
