.back-to-top {
    display: flex;
    flex-direction: row-reverse;
    grid-area: backtotop;

    a {
        border-bottom: none;
        font-size: $font-size--smalltype;
        transition: opacity 1s ease;
        opacity: 1;

        &:active {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            background: none;
            border-bottom-width: 0px;
        }

        &:focus {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            border-bottom-width: 0px;
        }

        &:hover {
            @include media('hover') {
                color: $color-gray10;
                background: none;
                text-decoration: none;
                border-bottom-width: 0px;
            }
        }

        &:hover .back-to-top__button svg circle {
            @include media('hover') {
                fill: $color-gray10;
            }
        }
        &:active .back-to-top__button svg circle {
            fill: $color-gray10;
        }

        &:hover .back-to-top__button__label {
            @include media('hover') {
                color: $color-gray10;
                border-bottom: $border-width-default $border-style-solid
                    $color-gray10;
            }
        }

        &:active .back-to-top__button__label {
            color: $color-gray10;
            border-bottom: $border-width-default $border-style-solid
                $color-gray10;
        }

        & svg {
            display: none;
        }
    }
    @include media('print') {
        display: none;
    }
}

.back-to-top__button__label {
    color: $color-gray7;
    border-bottom: $border-width-default $border-style-solid $color-gray7;
}

.back-to-top-stick {
    position: fixed;
    right: $measure--small1;
    bottom: $measure--threequarters;
    z-index: 100;
    text-decoration: none;
    width: $measure--medium3;
    opacity: 0.75 !important;
    overflow: hidden;

    @include media('<=mobile') {
        right: $measure--quarter;
        bottom: $measure--quarter;
    }
}

.back-to-top__button__arrow {
    color: $color-purewhite;
    background: $color-gray7;
    border-radius: 50%;
    padding: $measure--half;
    height: $measure--small4;
    width: $measure--small4;
    margin-left: 0.625rem;
    display: inline-block;
    text-align: center;
    vertical-align: middle;

    &:before {
        vertical-align: middle;
        padding-top: $measure--quarter;
        font-size: 0.625rem;
        border-top: $border-width-default solid $color-purewhite;
    }
}

.back-to-top-stick .back-to-top__button {
    width: $measure--medium3;
    height: $measure--medium3;
    border-radius: 50%;
    display: flex;
    position: relative;

    & svg {
        display: block;
        position: absolute;
    }

    & svg path {
        fill: none;
        stroke: $color-nearblack;
        stroke-width: 5;
    }

    & svg circle {
        fill: $color-gray7;
    }
}

.back-to-top-stick .back-to-top__button__arrow {
    padding-top: $measure--basic;
    display: block;
    border-radius: 50%;
    height: 2.556rem;
    width: 2.556rem;
    margin: auto;
    position: relative;
    background: none;

    &:before {
        border-top: $border-width-default solid $color-purewhite;
        padding-top: 0.375rem;
        vertical-align: initial;
        font-size: 0.8125rem;
    }
}

.back-to-top-stick .back-to-top__button__label {
    display: none;
}

.back-to-top-hide {
    opacity: 0 !important;
    transition: opacity 1s ease;
}
