.documentation {
    .content-wrapper {
        @include standard-margins();
        @include standard-grid();

        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-areas: 'main main main main main main';
        }

        @include media('>mobile') {
            grid-template-areas: '. . main main main main main main main main . .';
        }

        @include media('>between') {
            grid-template-areas: '. . . . . main main main main main main main main . . . . .';
        }

        > .content {
            grid-area: main;
        }
    }
}

.documentation--fullwidth {
    .content-wrapper {
        display: block;
    }

    .content-wrapper--wider {
        @include standard-margins();
        @include standard-grid();

        .related-content-grid {
            grid-column: 1 / -1;

            @include media('>between') {
                grid-column: 4 / 16;
            }

            @include media('>desktop') {
                grid-column: 3 / 17;
            }
        }
    }

    .promo-content {
        grid-column: 1 / -1;
    }

    .key-information--research {
        grid-column: 5/15;

        @include media('<mobile') {
            grid-column: 1/-1;
        }

        @include media('>mobile') {
            grid-column: 2/12;
        }

        @include media('>between') {
            grid-column: 4/17;
        }
    }
}
