.profile {
    .content-wrapper {
        @include standard-margins();
        @include standard-grid();

        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-areas: 'm m m m m m';
        }

        @include media('>mobile') {
            grid-template-areas: '. m m m m m m m m m m .';
        }

        @include media('>tablet') {
            grid-template-areas: '. . m m m m m m m m . .';
        }

        @include media('>between') {
            grid-template-areas: '. . . . . m m m m m m m m . . . . .';
        }

        > .content {
            grid-area: m;
        }
    }
}

.profile {
    .tabs--accordion {
        @include media('<=tablet') {
            @include standard-margins();
        }
    }

    .tabs__panel__content {
        margin: auto;
        max-width: $max-line-length;
    }

    .tabs--accordion .accordion__heading button .accordion__heading__indicator,
    .tabs--accordion
        .accordion__heading[data-open='true']
        button
        .accordion__heading__text {
        background: $color-gray10;
    }

    .tabs .tabs__links li a[aria-selected='true'] span {
        border-bottom-color: $color-gray10;
    }
}

/* 
   Acadamic profile is an example of 
   the default profile banner
*/
.profile__banner {
    background-color: $color-gray10;
    position: relative;

    h1 {
        @include media('<=mobile') {
            display: none;
        }
    }

    background: linear-gradient(
        45deg,
        $color-gray10 41%,
        #1a1919 56%,
        #121111 16%
    );

    @include media('>=tablet') {
        background: linear-gradient(
            29deg,
            #1e1e1e 16%,
            #1a1919 32%,
            #121111 30%
        );
    }

    @include media('>=desktop') {
        background: linear-gradient(
            45deg,
            $color-gray10 63%,
            #1a1919 77%,
            #121111 30%
        );
    }
}

.profile__banner--student,
.profile__banner--alumni,
.profile__banner--ambassador {
    background-color: $color-pink90;

    background: linear-gradient(
        41deg,
        $color-pink90 41%,
        #3d072e 56%,
        #300625 16%
    );

    @include media('>=tablet') {
        background: linear-gradient(
            40deg,
            $color-pink90 16%,
            #3d072e 32%,
            #300625 30%
        );
    }

    @include media('>=desktop') {
        background: linear-gradient(
            45deg,
            $color-pink90 63%,
            #3d072e 77%,
            #300625 30%
        );
    }
}

.profile__banner--press {
    background: $color-red60;

    background: linear-gradient(
        24deg,
        $color-red60 41%,
        #c11f21 56%,
        #ad1d1d 16%
    );

    @include media('>=tablet') {
        background: linear-gradient(
            35deg,
            $color-red60 16%,
            #c11f21 32%,
            #ad1d1d 30%
        );
    }

    @include media('>=desktop') {
        background: linear-gradient(
            45deg,
            $color-red60 63%,
            #c11f21 77%,
            #ad1d1d 30%
        );
    }
}

.profile__banner--staff {
    background-color: $color-blue90;

    background: linear-gradient(
        37deg,
        $color-blue90 41%,
        #00203b 56%,
        #001a30 16%
    );

    @include media('>=tablet') {
        background: linear-gradient(
            35deg,
            $color-blue90 16%,
            #00203b 32%,
            #001a30 30%
        );
    }

    @include media('>=desktop') {
        background: $color-blue90;

        background: linear-gradient(
            45deg,
            $color-blue90 63%,
            #00203b 77%,
            #001a30 30%
        );
    }
}

.profile__banner__wrapper {
    position: relative;
    color: $color-purewhite;
    @include standard-margins();

    @include media('<=mobile') {
        display: block;
    }

    @include media('>tablet') {
        @include standard-grid();
    }

    @include media('>between') {
        display: block;
    }
}

.profile__banner__content {
    padding-bottom: $measure--small4;

    @include media('<=mobile') {
        display: block;
    }

    @include media('>tablet') {
        grid-column: 2 / 12;
        display: flex;
        padding-bottom: $measure--large2;
    }

    @include media('>between') {
        @include standard-grid();
    }
}

.profile__banner__content__thumbnail {
    padding-top: $measure--small2;

    @include media('<=mobile') {
        display: flex;
        margin-bottom: $measure--basic;
    }

    @include media('>mobile') {
        display: block;
    }

    @include media('>tablet') {
        padding-top: $measure--medium1;
    }

    @include media('>between') {
        grid-column: 3 / 7;
    }

    @include media('>desktop') {
        grid-column: 3 / 6;
    }

    img {
        max-width: $measure--large2;

        @include media('>tablet') {
            max-width: $measure--huge3;
        }

        @include media('>desktop') {
            min-width: $measure--large3;
            min-height: $measure--large3;
            max-width: 12rem;
        }
    }

    figure {
        margin-right: $measure--small3;

        @include media('>mobile') {
            margin-right: $measure--small1;
        }

        @include media('>=tablet') {
            margin-right: $measure--medium2;
        }

        @include media('>between') {
            margin-right: 0;
        }
    }
}

.profile__banner__content__mobile-header {
    display: block;
    @include h1style();
    align-self: center;
    margin-top: $measure--small2;

    @include media('>mobile') {
        display: none;
    }
}

.profile__banner__content__text {
    @include media('>=tablet') {
        padding-top: $measure--medium8;
    }

    @include media('>between') {
        grid-column: 7 / 17;
        display: flex;
        flex-direction: column;
    }

    @include media('>desktop') {
        grid-column: 6 / 17;
        padding-left: $measure--threequarters;
    }
}

.profile__banner__content__text__dept {
    @include font-family('default');
    @include alphatype();
    margin-top: $measure--threequarters;
    font-weight: $font-weight-text;

    @include media('>mobile') {
        margin-top: $measure--small2;
    }

    a {
        @include anchor-inline--gray();
    }

    span:nth-of-type(1) {
        margin-right: $measure--basic;
    }

    span:nth-of-type(3),
    span:nth-of-type(4) {
        &:before {
            content: '\2219';
            margin: 0 $measure--half;
        }
    }
}

/* --- GLOBAL PROFILE STYLES --- */

.profile--global__tags {
    margin-top: $measure--medium3;
    padding: $measure--basic 0;
}

.profile--global__tags__heading {
    margin-bottom: $measure--small2;
    color: $color-gray8;
    font-weight: $font-weight-bold;
    max-width: $max-line-length;
}

.profile--global__tags__labels {
    @include media('>=tablet') {
        max-width: $max-line-length;
    }

    .cta-block__cta {
        margin: 0 $measure--small2 $measure--threequarters 0;
    }
}

.profile--global__details__heading {
    padding-bottom: $measure--half;
    border-bottom: $border-style-solid $border-width-default $color-gray2;

    span:first-child {
        font-size: $font-size--basic;
    }
}

.profile--global__details__heading__text {
    color: $color-gray7;
    margin-left: $measure--half;
    font-weight: $font-weight-bold;
}

.profile--global__details__content {
    a {
        span {
            margin-left: $measure--quarter;
        }
    }
}

.profile--global__personal-links,
.profile--global__other-contact-methods {
    .profile--global__details__content {
        margin-top: $measure--threequarters;
    }
}

.profile--global__contact-details {
    .profile--global__details__content {
        margin-top: $measure--quarter;
    }
}

.profile--global__details__content__column {
    ul {
        margin-left: 0;
    }

    li {
        list-style: none;
    }

    span {
        color: $color-red50;
        margin-right: $measure--half;
    }

    .fa-phone {
        transform: rotate(90deg);
    }
}

.profile--global__personal-links {
    @include media('<=mobile') {
        margin-top: $measure--medium3;
    }

    ul {
        margin-left: $measure--small2;
    }
}

.profile--global__details__content {
    @include content-style;
}

.profile--global__contact-details {
    margin-top: $measure--small4;

    @include media('>=tablet') {
        grid-column: 2 / 12;
        margin-top: $measure--small4;
    }

    @include media('>between') {
        grid-column: 2 / 18;
    }

    @include media('>=desktop') {
        grid-column: 2 / 10;
        padding-right: $measure--small2;
    }

    .profile--global__details__content {
        @include media('>=tablet') {
            display: flex;
        }
    }

    .profile--global__details__content__column {
        &:first-child {
            @include media('>=tablet') {
                margin-right: $measure--large2;
            }

            @include media('>desktop') {
                margin-right: $measure--medium3;
            }

            @include media('>large') {
                margin-right: $measure--large2;
            }
        }
    }
}

.profile--global__other-contact-methods {
    margin-top: $measure--medium3;

    @include media('>=tablet') {
        display: flex;
        margin-top: $measure--small4;
        grid-column: 2 / 12;
    }

    @include media('>between') {
        grid-column: 2 / 18;
    }

    @include media('>=desktop') {
        grid-column: 10 / 18;
    }

    .profile--global__details {
        @include media('>=tablet') {
            width: 50%;
        }

        &:first-child {
            @include media('>=tablet') {
                margin-right: $measure--small4;
            }

            @include media('>desktop') {
                margin-right: $measure--medium3;
            }
        }
    }
}

.profile--global__details__content__name {
    display: block;
    font-weight: $font-weight-bold;
}

.profile--global__date {
    color: $color-gray7;
    border-top: $border-style-solid $border-width-thick $color-gray7;
    padding-top: $measure--basic;
    margin-top: $measure--small4;

    @include media('>=tablet') {
        margin-top: $measure--medium3;
    }
}

/* --- END GLOBAL PROFILE STYLES --- */

/* --- ACADEMIC STYLES --- */

.profile--academic {
    @include standard-margins();
    margin-bottom: $measure--medium1;
    position: relative;

    @include media('<=mobile') {
        padding-left: $measure--threequarters;
    }

    @include media('>=tablet') {
        @include standard-grid();
        background: $color-purewhite;
        margin-top: -$measure--medium7;
    }
}

/* --- END ACADEMIC STYLES --- */

/* --- PRESS STYLES -- */

.profile--press__contact {
    @include standard-margins();
    position: relative;
    border-bottom: $border-style-solid $border-width-default $color-gray2;

    @include media('>mobile') {
        border-bottom: none;
    }

    @include media('>=tablet') {
        @include standard-grid();
        margin-top: -$measure--medium7;
    }
}

.profile--press__contact__about {
    margin-top: $measure--small4;

    h2 {
        margin-bottom: $measure--basic;
    }
}

.profile--press__contact__content {
    padding-bottom: $measure--basic;

    @include media('>mobile') {
        border-bottom: $border-style-solid $border-width-default $color-gray2;
    }

    @include media('>=tablet') {
        padding-left: $measure--small1;
    }

    @include media('>desktop') {
        padding-left: 0;
        max-width: $max-line-length;
        margin: auto;
    }
}

.profile--press__details__heading {
    border-bottom: none;

    .fa-address-card {
        color: $color-gray7;
    }
}

.profile--press__contact__details__content {
    margin-top: $measure--half;
}

.profile--press__contact__inner {
    grid-column: 2 / 12;
    padding-top: $measure--small4;
    background: $color-purewhite;

    @include media('<=mobile') {
        padding-left: $measure--threequarters;
    }

    @include media('>between') {
        grid-column: 3 / 17;
    }
}

.profile--press__contact__details__content__column {
    ul {
        @include media('>=tablet') {
            display: flex;

            li {
                margin-bottom: $measure--basic;
                margin-right: $measure--medium11;

                &:nth-child(2) {
                    margin-right: 0;
                }
            }
        }
    }
}

/* --- END PRESS STYLES --- */

/* --- ALUMNI STYLES --- */

.profile--alumni__details {
    @include media('>=tablet') {
        max-width: $max-line-length;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        padding: 0 $measure--small2 0 $measure--small2;
    }

    @include media('>desktop') {
        padding: 0;
    }

    .profile--global__details {
        &:nth-child(2) {
            margin-top: $measure--medium3;

            @include media('>=tablet') {
                margin-top: 0;
            }
        }
    }
}

.profile--alumni__contact {
    border-bottom: none;
}

.profile--alumni__details__column {
    @include media('>=tablet') {
        width: 50%;
    }

    &:first-child {
        @include media('>=tablet') {
            margin-right: $measure--medium7;
        }
    }
}

/* --- END ALUMNI STYLES --- */

/* --- STUDENT/AMBASSADOR STYLES --- */

.profile__banner--student,
.profile__banner--ambassador {
    .profile__banner__content {
        @include media('>=tablet') {
            padding-bottom: $measure--small4;
        }
    }
}

.profile--global__tags--student {
    margin-top: 0;

    .profile--global__tags__content {
        padding-bottom: $measure--small4;
    }
}

/* --- END STUDENT STYLES --- */

.profile__news-three-block {
    .related-content {
        @include standard-margins();

        @include media('>=tablet', '<=between') {
            // use flex to centre cards
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }

        @include media('>between') {
            @include standard-grid();
        }

        li:first-of-type {
            @include media('>between') {
                grid-column: 1 / 7;
            }
        }

        li:nth-of-type(2) {
            @include media('>between') {
                grid-column: 7 / 13;
            }
        }

        li:nth-of-type(3) {
            @include media('>between') {
                grid-column: 13 / -1;
            }
        }

        .card {
            @include media('>=tablet', '<=between') {
                // centre the cards
                max-width: 28rem; // this to be removed
            }

            @include media('<=between') {
                margin-bottom: $measure--basic;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

.profile__news-three-block__heading {
    grid-column: 1/-1;
    max-width: $max-line-length;
    margin-top: $measure--small4;
    margin-bottom: $measure--small4;
}
