@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin box-shadow {
    box-shadow: 0 $measure--quarter $measure--basic 0 rgba(0, 0, 0, 0.15);
}

@mixin background-color-and-scale(
    $background-color: $color-gray1,
    $scale: 1.03,
    $duration: $transition-duration--long
) {
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        transition: background-color $duration ease-in-out,
            transform $duration ease-in-out;
        @include media('reducemotion') {
            transition: none;
        }
    }

    &:hover {
        @include media('hover') {
            &:after {
                background-color: $background-color;
                transform: scale($scale);
            }
        }
    }

    .card__inner {
        z-index: 0;
        position: relative;
    }
}

@mixin thumbnail-gradient-scale($scale: 1.04, $background-opacity: 0.1) {
    &:hover {
        @include media('hover') {
            background-image: none;

            img {
                transform: scale($scale);
            }

            .thumbnail__image-container {
                &:after {
                    background-color: rgba(0, 0, 0, $background-opacity);
                }
            }
        }
    }

    .thumbnail__image-container {
        overflow: hidden;
        position: relative;
        transition: transform $transition-duration ease-in;
        width: $measure--huge3;
        height: $measure--huge1;

        @include media('reducemotion') {
            transition: none;
        }

        &:after {
            background-color: transparent;
            content: '';
            display: block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            transition: background-color $transition-duration ease-in;
            width: 100%;
        }

        img {
            transition: transform $transition-duration ease-in;
            object-fit: cover;
            height: 100%;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }
}

@mixin card-shared-properties(
    $title-color: $color-gray7,
    $paragraph-color: $color-gray10
) {
    position: relative;
    transition: none;
    padding: $measure--basic;

    &:hover {
        @include media('hover') {
            background-image: none;
        }
    }

    &:focus,
    &:active {
        &:after {
            @include outline-properties();
        }
    }

    &:visited {
        .title {
            color: $color-inline-link--visited;
        }
    }

    .card__title {
        @include h3style();
        @include betatype();
        color: $title-color;
    }

    p {
        @include alphatype();
        color: $paragraph-color;
    }
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
@mixin card-with-thumbnail {
    display: block;

    @include media('>=tablet') {
        display: flex;
    }

    > :nth-child(2) {
        margin-top: $measure--basic;

        @include media('>=tablet') {
            margin-top: 0;
        }
    }
}

/* 
    USAGE:

    $pseudo: both / before / after
    $background-color: any hex color
    $skew: a degree value
    
    Background color must match background color of element mixin is applied to
*/

@mixin angled-edge(
    $pseudo: both,
    $background-color: $color-gray5,
    $skew: 4deg
) {
    position: relative;

    &:before,
    &:after {
        position: absolute;
        left: 0;
        right: 0;
        z-index: -1;
        content: '';
        display: none;
        background-color: $background-color;
        height: $measure--medium11;
        transform: skewY($skew);
    }

    &:before {
        top: -$measure--medium3;

        @if ($pseudo == before or $pseudo == both) {
            display: block;
        }
    }

    &:after {
        bottom: -$measure--medium3;

        @if ($pseudo == after or $pseudo == both) {
            display: block;
        }
    }
}

@mixin gradient-border($color1: $color-red50, $color2: $color-pink60) {
    border: $measure--threequarters solid;
    border-image-source: linear-gradient(45deg, $color1, $color2);
    border-image-slice: 1;
}

////
// this button mixin is used on: testimonial slider, image-gallery
////
@mixin control-button($color1: $color-red50) {
    color: $color1;
    border: $border-width-hairline solid $color1;
    padding: $measure--threequarters;
    font-weight: $font-weight-normal;

    &:hover {
        @include media('hover') {
            color: $color-purewhite;
            background: $color1;
        }
    }

    &:focus,
    &:active {
        color: $color-purewhite;
        background: $color1;
        @include anchor-default-outline--dark();
    }

    &:disabled {
        color: $color-gray7;
        background: $color-purewhite;
        border: none;
        opacity: 0.7;
    }
}
