.listing {
    background-image: linear-gradient($color-gray1, $color-gray2);

    .banner-text {
        background: $color-gray1;
        color: $color-nearblack;
    }
}

.finder {
    @include standard-grid();

    @include media('<=mobile') {
        grid-template-rows: 3;
        grid-template-areas:
            'query query query query query query'
            'filters filters filters filters filters filters'
            'results results results results results results';
    }

    @include media('>mobile', '<=tablet') {
        grid-template-rows: 3;
        grid-template-areas:
            'query query query query query query query query query query query query'
            'filters filters filters filters filters filters filters filters filters filters filters filters'
            'results results results results results results results results results results results results';
    }

    @include media('>tablet') {
        grid-template-rows: 2;
        grid-template-areas:
            'query query query query query query query query query query query query'
            'filters filters filters filters filters results results results results results results results';
    }

    @include media('>between') {
        grid-template-rows: 2;
        grid-template-areas:
            'query query query query query query query query query query query query query query query query query query'
            'filters filters filters filters filters filters . results results results results results results results results results results results';
    }

    @include media('>desktop') {
        grid-template-rows: 2;
        grid-template-areas:
            'query query query query query query query query query query query query query query query query . .'
            'filters filters filters filters filters filters . results results results results results results results results results results results';
    }

    @include media('>large') {
        grid-template-rows: 2;
        grid-template-areas:
            'query query query query query query query query query query query query query query query . . .'
            'filters filters filters filters filters filters . results results results results results results results results results results results';
    }

    > .content {
        grid-area: main;
    }
}

.wrapper--finder--hub .finder {
    @include media('<=mobile') {
        grid-template-rows: 4;
        grid-template-areas:
            'title title title title title title'
            'select select select select select select'
            'query query query query query query'
            'results results results results results results';
    }

    @include media('>mobile', '<=tablet') {
        grid-template-rows: 4;
        grid-template-areas:
            'title title title title title title title title title title title title'
            'select select select select select select select select select select select select'
            'query query query query query query query query query query query query'
            'results results results results results results results results results results results results';
    }

    @include media('>tablet') {
        grid-template-rows: 3;
        grid-template-areas:
            'title title title title title title title title title title title title'
            'select select select select query query query query query query query query'
            '. . . results results results results results results results results results';
    }

    @include media('>between') {
        grid-template-rows: 3;
        grid-template-areas:
            'title title title title title title title title title title title title title title title title title title'
            'select select select select select query query query query query query query query query query query query query'
            '. . . . . results results results results results results results results results results results results results';
    }
}

.finder__title {
    grid-area: title;
    text-align: center;
}

.finder__query {
    grid-area: query;
    margin-top: $measure--basic;
    position: relative;
    padding: $measure--small2;
    background: $color-purewhite;

    & > div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 100%;
    }
}

.finder__query__input {
    @include font-family('default');
    @include body-font-features();
    @include gammatype();

    display: flex;
    border-image-source: linear-gradient(45deg, $color-pink90, $color-red50);
    border-image-slice: 1;
    border-width: $border-width-thick;
    border-style: $border-style-solid;
    padding: 0;

    &:focus-within {
        @include outline-properties();
    }

    flex-grow: 1;
    position: relative;

    input {
        @include font-family('default');
        @include body-font-features();
        @include gammatype();

        font-family: $font-family-plex-sans;
        font-weight: $font-weight-text;
        padding: $measure--half;
        width: 100%;
        min-width: 0; // Constrains width in Firefox
        height: 100%;
        grid-area: input;
        border: $border-width-hairline solid transparent;
        font-style: normal;

        &:focus {
            outline: 0;
            font-style: normal;
            font-weight: $font-weight-bold;
            color: $color-gray10;
            &::placeholder,
            &::-ms-input-placeholder {
                opacity: 0;
            }
        }
        &::placeholder {
            font-style: italic;
        }
        &:placeholder-shown {
            color: $color-gray7;
        }
    }

    .finder__clear {
        height: 100%;
        position: absolute;
        right: $measure--basic;
        top: 0;
        width: $measure--small4;
        cursor: pointer;

        &:focus {
            outline: 0;
        }

        .icon {
            border-radius: 50%;
            background: $color-gray1;
            color: $color-gray7;
            width: $measure--small4;
            height: $measure--small4;
            position: relative;
            &:hover {
                @include media('hover') {
                    cursor: pointer;
                }
            }
            @include media('<tablet') {
                width: $measure--small2;
                height: $measure--small2;
            }
            &:before {
                position: relative;
                top: #{$measure--quarter + $border-width-hairline};
                @include media('<=tablet') {
                    top: 0;
                }
            }
        }

        .finder__icon--close {
            background: transparent;
            position: relative;
            top: $measure--half;
            left: $measure--quarter;
            @include media('<tablet') {
                top: $measure--quarter;
                left: 0;
            }
        }
    }
}

.finder__query__icon--wrapper {
    background: $color-purewhite;
    padding: #{$measure--quarter + $border-width-hairline} $measure--half $measure--half
        $measure--basic;
}

.wrapper--finder--hub .finder__query__icon--wrapper {
    padding-top: #{$measure--half + $border-width-hairline};
}

.finder__query__submit {
    @include gammatype();
    @include anchor-inline--notvisited();
    @include cta-basic-light-bg();
    border-radius: 0;

    &:hover {
        cursor: pointer;
    }
    @include media('<tablet') {
        padding: $measure--basic $measure--small1;
        &:hover {
            padding: $measure--basic $measure--small1;
        }
    }

    span {
        margin: auto;
    }

    .finder__query__submit__text {
        @include media('<=tablet') {
            @include screenreader-only();
        }
    }

    .finder__query__submit__icon {
        padding: 0 $measure--half 0 0;
        font-weight: $font-weight-medium;

        @include media('<=tablet') {
            display: block;
            padding: 0;
        }
    }

    flex-shrink: 0;
    margin-left: $measure--small4;
    @include media('<=tablet') {
        margin-left: $measure--basic;
    }
    box-shadow: none;
}

.finder__didyoumean__button {
    @include anchor-inline--notvisited();
}

.finder__filter {
    background-color: $color-purewhite;
    legend {
        @include h3style();
        position: relative;
        display: block;
        width: 100%;
        background-color: $color-purewhite;
        padding: 0;

        button {
            //@include anchor-inline--notvisited();
            border-bottom-color: $color-purewhite;
            width: 100%;
            background-image: none;
            padding: $measure--threequarters $measure--small2;

            &:active,
            &:focus {
                &:focus {
                    outline-offset: -$measure--quarter;
                }
            }

            &:hover {
                .icon {
                    color: $color-red50;
                    transition: color 0.6s;
                }
            }

            & > span {
                color: $color-nearblack;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-between;
            }
        }

        button[aria-expanded='true'] {
            .icon {
                color: $color-red50;
            }
        }
    }

    label {
        width: 100%;
        cursor: pointer;
    }

    input:not(:disabled) {
        cursor: pointer;
    }

    input[type='radio'] {
        align-self: flex-start;
        display: block;
        margin-right: $measure--basic;
        margin-top: $measure--half;
    }

    legend[data-open='true'] {
        &:after {
            content: '';
            position: absolute;
            width: calc(100% - $measure--medium3);
            border-top: $border-width-hairline solid $color-gray3;
            left: $measure--small2;
            bottom: 0;
        }
    }

    legend[data-open='true'] + ul {
        display: block;
    }

    legend[data-open='false'] + ul {
        display: none;
    }

    ul {
        position: relative;
        list-style-type: none;
        margin-left: 0;
        max-width: unset;
        background-color: $color-purewhite;
        margin-top: 0;
        padding: $measure--threequarters $measure--small2;
        //border-top: $border-width-default solid $color-nearblack;
    }

    li {
        align-items: center;
        display: flex;
        flex-direction: row;
    }
}

.finder__radio {
    margin-top: $measure--quarter;
}

.finder__results__course {
    .clearing_label {
        margin-bottom: $measure--basic;

        @include media('>tablet') {
            order: 1;
            min-width: 24%;
        }

        p {
            @include media('<=tablet') {
                display: inline;
            }
            margin-top: 0;
            padding: $measure--threequarters;
            background-color: $color-yellow40;
            font-weight: bold;

            span {
                margin-left: $measure--half;
            }
        }
    }
}

.finder__togglebox {
    padding: $measure--basic;
    background-color: $color-purewhite;
}

.finder__toggle {
    label {
        font-weight: bold;
        display: flex;
    }
    input {
        opacity: 0;
        width: 0;
        height: 0;

        &:checked + .toggle:before {
            transform: translateX(
                $measure--threequarters - $border-width-default
            );
        }

        &:focus + .toggle {
            outline-width: $border-width-default;
            outline-style: dotted;
            outline-offset: $border-width-thick;
            outline-color: $color-gray11;
        }
    }
}

.toggle {
    border-radius: $measure--medium3;
    background-color: $color-gray1;
    position: relative;
    display: inline-block;
    width: $measure--small4;
    height: $measure--small1 + $border-width-default;
    transition: background-color 0.3s;
    border: $border-width-hairline solid $color-gray7;

    &:before {
        position: absolute;
        content: '';
        border-radius: 50%;
        height: $measure--small1;
        width: $measure--small1;
        background-color: $color-purewhite;
        transition: transform 0.3s;
        border: $border-width-hairline solid;
    }
}

.toggleOpen {
    background-color: $color-yellow40;
}

.finder__results__course {
    a {
        @include anchor-reset-properties();
        @include anchor-border-reset();
        @include underline-transition--title();

        &:hover,
        &:active {
            @include media('hover') {
                background-color: $color-purewhite;
                background-image: none;
                outline: none;
            }
        }

        &:focus {
            outline: none;
            background-image: none;
        }
    }

    .underline-transition__title {
        @include media('>tablet') {
            margin-top: 0;
        }
    }
}

.finder__appliedfilters__wrap {
    background-color: $color-purewhite;
    margin-top: 0;
}

.finder__appliedfilters {
    grid-area: appliedFilters;
    display: none;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    margin-left: 0;
    margin-bottom: $measure--basic;
    max-width: unset;
    padding: $measure--threequarters $measure--small2;

    @include media('>tablet') {
        display: block;
    }

    li {
        margin-right: $measure--small4;

        &:last-of-type {
            margin-right: 0;
        }
    }

    button {
        @include anchor-inline--notvisited();
        text-align: left;

        span:first-of-type {
            display: flex;
        }

        .icon {
            margin-right: $measure--half;
        }
    }
}

.finder__query__suggestions {
    background-color: $color-purewhite;
    box-shadow: rgba(0, 0, 0, 0.25) 0px $measure--basic $measure--medium3 -$measure--threequarters;
    border: $border-width-hairline $border-style-solid $color-gray2;
    border-top: 0;
    border-radius: 0 0 $measure--half $measure--half;
    color: $color-purewhite;
    font-weight: $font-weight-bold;
    left: 0;
    list-style: none;
    margin-left: 0;
    max-width: unset;
    position: absolute;
    top: $measure--medium1;
    width: 100%;
    z-index: 1;

    li {
        margin-top: 0;
        font-weight: $font-weight-normal;

        button {
            height: 100%;
            padding: $measure--half $measure--small4 $measure--half
                $measure--small4;
            text-align: left;
            width: 100%;
            color: $color-text;

            &:focus {
                outline-color: $color-nearblack;
                outline-offset: (-$border-width-default);
                outline-style: $border-style-dotted;
                outline-width: $border-width-default;
                text-decoration: underline;
            }

            &:active {
                @include outline-properties();
                outline-offset: 0;
            }

            &:hover {
                background-color: $color-linkblue;
                text-decoration: underline;
                color: $color-purewhite;
            }

            .finder__suggestion {
                font-weight: bold;
            }
        }
    }
}

.finder__query__suggestions.hide {
    display: none;
}

.finder__filters {
    @include media('>tablet') {
        margin-top: $measure--basic;
    }

    @include media('<=tablet') {
        background-color: $color-gray3;
        margin-bottom: $measure--medium3;
        .finder__clear {
            display: none;
        }
    }
}
.finder__results {
    @include media('<=tablet') {
    }
    @include media('>tablet') {
        margin-top: $measure--basic;
    }
}

.finder__filters {
    grid-area: filters;
    display: none;

    @include media('>tablet') {
        display: block;
    }

    fieldset:last-child {
        .finder__select {
            margin-bottom: $measure--basic;
        }
    }
}
.finder__filters {
    .finder__filters__heading {
        .fa-sliders-h {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        .finder__clear {
            @include media('>tablet') {
                display: none;
            }
            @include media('<=tablet') {
                display: block;
            }
        }
    }
}

.finder__filters__heading,
.finder__applied__filters__heading {
    display: flex;
    justify-content: space-between;
    background-color: $color-gray3;
    padding: $measure--threequarters $measure--small2;
    font-size: $font-size--delta;

    h2 {
        margin-top: 0;
        font-size: $font-size--delta;
    }
}

.finder__applied__filters__heading {
    @include media('<=tablet') {
        display: none;
    }
}

.finder__filters {
    .finder__clear {
        font-size: $measure--basic;
        @include cta-outlined-light-bg();
        padding: $measure--half $measure--half;
        border-color: $color-red50;
        color: $color-red50;
        margin-right: $measure--basic;
        background-color: $color-purewhite;

        .fa-times {
            margin-right: $measure--quarter;
        }

        &:focus {
            background-color: $color-purewhite;
            color: $color-red50;
            border-color: $color-red50;
        }

        &:hover,
        &:active {
            color: $color-purewhite;
            background-color: $color-red50;
            border-color: $color-red50;
        }

        @include media('<tablet') {
            display: none;
        }
    }
}

.finder__filters__heading__btn-icon {
    display: flex;
}

.finder__mobilefilters {
    grid-area: filters;
    display: block;

    .finder__mobilefilters__apply {
        @include gammatype();
        @include anchor-inline--notvisited();
    }

    .finder__mobilefilters__toggle {
        padding: $measure--half;
        @include gammatype();
        @include anchor-inline--notvisited();

        margin-top: $heading-margin;
        background-color: $color-purewhite;
        border: $border-width-default solid $color-gray7;
        color: $color-nearblack;

        &:focus {
            border: $border-width-default solid $color-gray7;
            color: $color-nearblack;
            background: $color-purewhite;
        }

        &:hover,
        &:active {
            background: $color-gray7;
            color: $color-purewhite;
        }
    }

    .finder__mobilefilters__toggle[data-filters-applied='true'] {
        border-color: $color-red50;

        .finder__mobilefilters__toggle__filterNo {
            color: $color-red50;
        }
    }

    .finder__results__summary__heading-wrap {
        display: inline;
        float: left;
        font-size: $font-size--delta;
        margin-top: $measure--small2;
    }

    @include media('>tablet') {
        display: none;
    }

    &[data-open='true'] {
        background: $color-purewhite;
        bottom: 0;
        height: 100vh;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        width: 100vw;
        z-index: 3;

        .finder__mobilefilters__filters {
            -webkit-overflow-scrolling: touch;
            height: 100vh;
            overflow-y: scroll;
            position: relative;
            width: 100vw;
        }

        .finder__mobilefilters__filters__content {
            padding-bottom: $measure--medium9;
        }

        .finder__filters {
            @include media('>tablet') {
                padding-left: $measure--small2;
                padding-right: $measure--small2;
            }
        }

        .finder__mobilefilters__apply {
            background-color: $color-red50;
            border: 0;
            color: $color-purewhite;
            margin-top: $measure--basic;
            padding-bottom: $measure--basic;
            padding-top: $measure--basic;
            width: 100%;
            position: fixed;
            bottom: 0;

            &:hover,
            &:active {
                background: $color-red70;
                outline-color: $color-purewhite;
                outline-offset: -$border-width-thick;
            }

            &:focus {
                outline-color: $color-purewhite;
                outline-offset: -$border-width-thick;
            }

            .fa-chevron-left {
                position: absolute;
                left: $measure--basic;
                padding: $measure--quarter 0;
                border-bottom: none;
            }

            & > span {
                border-bottom: $border-width-default $border-style-solid
                    $color-purewhite;
            }
        }

        .finder__filters,
        .finder__mobilefilters__apply {
            display: block;
        }

        .finder__mobilefilters__toggle {
            display: none;
        }

        .finder__results__summary__heading-wrap {
            display: none;
        }
    }

    &[data-open='false'] {
        text-align: right;

        .finder__filters,
        .finder__mobilefilters__apply {
            display: none;
        }

        .finder__mobilefilters__toggle {
            display: inline-block;
        }
    }
}

.finder .finder__results {
    grid-area: results;
    @include media('>tablet', '<=between') {
        margin-left: $measure--medium3;
    }

    .pagination__wrapper {
        max-width: unset;
    }

    .pagination__controls {
        justify-content: center;
    }
}

.finder__results {
    .finder__results__summary__heading-wrap {
        font-size: $font-size--delta;
        @include media('<=tablet') {
            display: none;
        }
    }
}

.finder__results__list {
    list-style-type: none;
    margin-left: 0;
    max-width: unset;
}

.highlightText {
    background-color: $color-red10;
}

.finder__results__updating {
    @include epsilontype();
    margin-top: $heading-margin;

    .icon {
        color: $color-red50;
    }
}

.finder__results__card {
    margin-top: $measure--small4;
    padding-bottom: $measure--small4;
    border-bottom: $border-width-hairline solid $color-gray6;

    a {
        @include thumbnail-gradient-scale;
        @include background-color-and-scale;
        @include underline-transition;
        @include card-shared-properties;

        color: $color-text;
        display: flex;
        flex-direction: column;

        &:hover {
            &:after {
                background-color: $color-purewhite;
            }
        }

        @include media('>tablet') {
            flex-direction: row;
        }

        .finder__results__card__description {
            @include smalltype();
            color: $color-red50;
            margin-top: $measure--half;
        }

        p {
            // @include alphatype();
            color: $color-gray10;
        }

        .finder__results__card__tag {
            @include smalltype();
            color: $color-gray8;
            margin-top: $content-element-margin;

            .icon {
                color: $color-red50;
                margin-right: $measure--half;
                font-weight: $font-weight-medium;
            }
        }
    }
}

.finder__results__bestbet {
    a .finder__results__card__description {
        color: $color-text;
    }
}

.finder__selectbox {
    margin: $measure--basic;
    background-color: $color-purewhite;
    margin-bottom: 0;

    select {
        font-feature-settings: initial;
    }
}

.finder__select {
    height: auto;
    margin-top: $measure--basic;
    position: relative;
    color: $color-nearblack;
    select {
        @include alphatype();
        padding: $measure--threequarters $measure--small4
            $measure--threequarters $measure--threequarters;
        position: relative;
        border: $border-width-default $border-style-solid $color-pink90;
        color: $color-nearblack;
        bottom: $measure--quarter;
        font-weight: $font-weight-medium;
        width: 100%; // Stops select menu stretching beyond grid
        background: none;
        appearance: none;
        -ms-expand: none;
        &:focus {
            @include outline-properties();
        }
    }
    &:before {
        @include font-family('icon');
        font-size: $font-size--icon;
        content: '\f078';
        font-weight: $font-weight-medium;
        margin-top: $measure--basic;
        display: inline-block;
        vertical-align: middle;
        line-height: normal;
        position: absolute;
        top: $measure--small5;
        right: $measure--small1;
    }
    @include media('<tablet') {
        margin-bottom: $measure--half;
    }
}

// Form rendering is slightly different across browsers. This helps align select menu with overline in Firefox.
@-moz-document url-prefix() {
    .finder__select select {
        margin-left: -$measure--quarter;
    }
}

.finder__select--level {
    grid-area: select;
    background: $color-purewhite;
}

.finder__select__overline {
    @include smalltype();
    color: $color-red50;
    font-weight: $font-weight-text;
    display: block;
    padding-bottom: $measure--basic;
}

.finder__input-text__wrapper {
    display: flex;
    flex: 1;
    background: $color-purewhite;
    padding: $measure--half $measure--threequarters $measure--quarter
        $measure--quarter;
}

.finder__input-text__label {
    display: none;
}

.finder__icon {
    background: $color-purewhite;
    font-weight: $font-weight-normal;
    color: $color-gray7;
    margin-top: 50%;
}

.finder__icon--close {
    color: $color-gray10;
    background: $color-gray1;
}

.finder__icon--close__wrapper {
    font-weight: $font-weight-normal;
}

//Course Path Card fix on hover

.finder__results__card {
    position: relative;
    z-index: 0;
}

// Background styles when text input is active/inactive.
// .background-fill {
//     position: absolute;
//     height: 100%;
//     width: 100%;
//     left: 0;
//     top: 0;
