// Edge styles

.edge {
    .finder__query__input {
        input {
            &:focus {
                @include outline-properties();
            }
        }

        .finder__clear {
            &:focus {
                @include outline-properties();
            }
        }
    }

    .finder__select select {
        &:focus {
            @include outline-properties();
        }
    }
}
