// These rulesets are used on the Business Case Study
// and on Research Case Study paint layouts

.wrapper__case-study__overview__main {
    @include standard-grid();

    @include media('>mobile') {
        @include standard-margins();
    }
}

.case-study__overview__main {
    grid-column: 1 / -1;
    padding: $measure--basic 0;
    background: $color-gray13;
    @include sixteen-grid();

    @include media('>between') {
        grid-column: 2 / 18;
        padding: $measure--small2 0 $measure--medium1 0;
    }

    @include media('<=mobile') {
        grid-template-areas: 'c c c c c c';
    }

    @include media('>mobile') {
        grid-template-areas: '. c c c c c c c c c c .';
    }

    @include media('>between') {
        grid-template-areas: '. . c c c c c c c c c c c c . .';
    }

    @include media('>desktop') {
        grid-template-areas: '. . . c c c c c c c c c c . . .';
    }
}

.case-study__overview__main__details {
    grid-area: c;

    @include media('<=mobile') {
        @include standard-margins();
    }
    @include media('>mobile') {
        margin: 0;
    }
}

.case-study--research {
    .case-study__overview__main {
        padding: $measure--small2 0 $measure--medium1 0;
    }
}

.case-study--business {
    .case-study__overview__main {
        @include media('>between') {
            padding-top: $measure--small4;
        }
    }

    .case-study__knowledge {
        .cta {
            margin: $measure--small1 0 $measure--medium3 0;
        }
    }
}

.case-study__content {
    grid-column: 1 / -1;
    margin-bottom: $measure--basic;

    @include media('>mobile') {
        grid-column: 2 / 12;
    }

    @include media('>tablet') {
        margin-bottom: 0;
    }

    @include media('>between') {
        grid-column: 4 / 16;
    }

    @include media('>desktop') {
        grid-column: 6 / 14;
    }

    h2 {
        margin-top: $measure--small2;
    }
}

.case-study--research__details,
.case-study__grey__wrap__container__content {
    padding-bottom: $measure--medium4;
    grid-column: 1 / -1;

    @include media('>mobile') {
        grid-column: 3 / 11;
    }

    @include media('>mobile', '<tablet') {
        grid-column: 2 / 12;
    }

    @include media('>=tablet') {
        grid-column: 2 / 12;
    }

    @include media('>between') {
        grid-column: 4 / 16;
    }

    @include media('>desktop') {
        grid-column: 6 / 14;
    }

    .icon {
        color: $color-red50;
    }
}

.case-study__overview__main__details__company-cards {
    @include media('>=tablet') {
        display: flex;
        margin-bottom: $measure--basic;
    }
}

.case-study__company-card {
    margin-bottom: $measure--basic;
    display: flex;
    width: 100%;
    border: $border-width-hairline $border-style-solid $color-gray3;
    border-bottom: $border-width-hairline $border-style-solid $color-gray3;
    background: $color-purewhite;
    max-height: $measure--large3;
    min-height: $measure--large3;
    color: $color-gray7;
    transition: none;

    @include media('>tablet') {
        margin-bottom: 0;
        width: 20rem;
    }

    &:first-child {
        margin-right: $measure--basic;
    }

    &:hover,
    &:focus,
    &:visited {
        color: $color-nearblack;
        background-image: none;
        background: $color-purewhite;
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        @include box-shadow;

        .company-card__name {
            &:after {
                border-bottom-style: solid;
                border-bottom-width: $border-width-thick;
                border-bottom-color: $color-nearblack;
            }
        }
    }

    figure {
        margin-top: 0;
        overflow: hidden;
    }

    .figure--image {
        width: $measure--medium11;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        padding: $measure--basic;

        picture {
            text-align: center;
            height: 100%;

            img {
                max-height: 100%;
                height: auto;
                max-width: 100%;
                width: auto;
            }
        }

        @include media('>tablet') {
            width: $measure--large4;
        }

        &:after {
            content: '';
            position: absolute;
            top: $measure--basic;
            right: 0;
            height: $measure--medium9;
            width: 1px;
            background-color: $color-gray2;
        }
    }
}

.company-card__name {
    padding-bottom: $measure--quarter;
    position: relative;

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: $border-width-thick;
    }

    .fa-external-link {
        margin-left: 0;
    }
}

.company-card__name-title {
    margin-bottom: $measure--half;
    @include font-family('heading');
}

.company-card__funder {
    margin-bottom: $measure--quarter;
}

.case-study__company-card__details {
    display: flex;
    align-items: center;
    padding-left: $measure--basic;
}

.case-study__overview__main__details {
    .label {
        font-weight: $font-weight-medium;
    }
}

.case-study__overview__main__details__result {
    display: inline-block;

    p {
        float: left;
        margin-right: $measure--half;
    }
}

.case-study__overview__multimedia-banner {
    margin-top: $measure--small2;
    @include standard-grid();
    @include standard-margins();
}

.case-study__overview__multimedia-banner__video {
    grid-column: 1 / -1;

    @include media('>tablet') {
        grid-column: 2 / 12;
    }

    @include media('>between') {
        grid-column: 4 / 16;
    }

    .youtube-video {
        padding: 0;
    }

    figcaption {
        display: inline-block;
        margin-bottom: 0; // override pattern
    }

    figcaption time {
        float: left;
    }

    .embedded-video {
        padding-bottom: 0;
        padding-top: 0;
    }
}

.case-study__overview__multimedia-banner__image {
    grid-column: 1 / -1;

    @include media('>tablet') {
        grid-column: 2 / 12;
    }

    @include media('>between') {
        grid-column: 4 / 16;
    }
}

.case-study__content__container {
    @include standard-grid();
    @include standard-margins();
}

.case-study__content__share-icons__wrap {
    margin: $measure--small4 0;
}

.case-study__content__share-icons {
    position: relative;
    border-bottom: $border-width-hairline solid $color-gray2;
    border-top: $border-width-hairline solid $color-gray2;
}

.case-study__content__share-icons__icons {
    ul {
        list-style-type: none;
        margin: $measure--small2 0;
        display: flex;
        max-width: none;
    }

    li {
        margin-right: $measure--threequarters;
        margin-top: 0;
    }

    li.copyLink {
        margin-right: 0;
    }
}

.case-study__grey__wrap {
    background: $color-gray1;
    padding: $measure--small1 0 0;
}

.case-study__grey__wrap__container {
    @include standard-grid();
    @include standard-margins();
}

.case-study__related-case-studies__wrap {
    h2,
    h3 {
        text-align: center;
    }
}

.case-study__related-case-studies {
    @include standard-margins();

    .related-content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: $measure--basic;

        @include media('<between') {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    .related-content__heading {
        margin-top: $measure--small4;
    }
}

.case-study--research__details__wrap {
    overflow: hidden;
    background: $color-gray1;
}

.case-study--research__details__wrap__container {
    @include standard-margins();
    @include standard-grid();
}

.case-study--research__details__section {
    background: $color-purewhite;
    padding: $measure--basic;
    margin-top: $measure--basic;

    .icon {
        margin-right: $measure--half;
    }

    .label {
        color: $color-gray7;
        font-weight: $font-weight-medium;
    }
}

.case-study__contact-details__card {
    .related-content {
        @include media('>=tablet') {
            display: flex;
        }

        .card {
            &:nth-child(2) {
                margin-top: $measure--basic;

                @include media('>=tablet') {
                    margin-top: 0;
                }
            }

            @include media('>=tablet') {
                width: 50%;
                margin-right: $measure--small2;
            }
        }
    }
}
