////
/// Anchor mixins
/// @group abstracts
////

@mixin anchor-reset-properties() {
    background-color: initial;
    border-bottom-color: initial;
    border-bottom-style: none;
    border-bottom-width: 0;
    color: initial;
    cursor: pointer;
    outline-color: initial;
    outline-offset: 0;
    outline-style: none;
    outline-width: 0;
    text-decoration: none;
}

@mixin anchor-reset() {
    @include anchor-reset-properties();

    &:hover {
        @include media('hover') {
            @include anchor-reset-properties();
        }
    }

    &:focus,
    &:active {
        @include anchor-reset-properties();
    }
}

@mixin outline-properties() {
    outline-width: $border-width-default;
    outline-style: $border-style-dotted;
    outline-offset: $border-width-thick;
    outline-color: $color-outline; // black
}

@mixin outline-properties--bright() {
    @include outline-properties();
    outline-color: $color-outline--dark; // white
}

@mixin anchor-default-outline--dark() {
    &:focus,
    &:active {
        @include outline-properties();
    }
}

@mixin anchor-default-outline--bright() {
    &:focus,
    &:active {
        @include outline-properties--bright();
    }
}

@mixin anchor-hover-background-settings {
    transition: background-size $transition-duration $transition-function;
    background-repeat: no-repeat;
    background-size: 100% 0;
    background-position: 0 100%;
    background-image: linear-gradient(
        120deg,
        $color-inline-link 0%,
        $color-inline-link 100%
    );

    @include media('reducemotion') {
        transition: none;
    }

    &:hover {
        @include media('hover') {
            background-size: 100% 100%;
            background-image: linear-gradient(
                120deg,
                $color-inline-link 0%,
                $color-inline-link 100%
            );
        }
    }
}

@mixin anchor-hover-background() {
    @include anchor-hover-background-settings;
}

@mixin anchor-hover-background--dark() {
    @include anchor-hover-background-settings;
    background-image: linear-gradient(
        120deg,
        $color-inline-link--dark 0%,
        $color-inline-link--dark 100%
    );
}

@mixin anchor-inline-color() {
    color: $color-inline-link;

    &:visited {
        color: $color-inline-link--visited;
    }

    &:focus {
        color: $color-inline-link;
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;
        }
    }

    &:active {
        color: $color-text--dark;
        background-color: $color-inline-link;
    }
}

@mixin anchor-inline-color-notvisited() {
    @include anchor-inline-color();

    &:visited {
        color: $color-inline-link;
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;
        }
    }

    &:active {
        color: $color-text--dark;
    }
}

@mixin anchor-inline() {
    @include anchor-reset();
    @include anchor-hover-background();
    @include anchor-inline-color();
    @include anchor-default-outline--dark();
    border-bottom-color: $color-inline-link;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;

    &:visited {
        border-bottom-color: $color-inline-link--visited;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    &:focus {
        border-bottom-color: $color-inline-link;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: $color-inline-link;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }
    }

    &:active {
        border-bottom-color: $color-inline-link;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }
}

@mixin anchor-inline--notvisited() {
    @include anchor-inline();
    @include anchor-inline-color-notvisited();
    border-bottom: $color-inline-link $border-style-solid $border-width-default;

    &:visited {
        border-bottom: $color-inline-link $border-style-solid
            $border-width-default;
    }

    &:focus {
        border-bottom: $color-inline-link $border-style-solid
            $border-width-default;
    }

    &:hover {
        @include media('hover') {
            border-bottom: $color-inline-link $border-style-solid
                $border-width-default;
        }
    }

    &:active {
        border-bottom: $color-inline-link $border-style-solid
            $border-width-default;
    }
}

@mixin anchor-inline--dark() {
    @include anchor-inline();
    @include anchor-hover-background--dark();

    border-bottom-color: $color-inline-link--dark;
    color: $color-inline-link--dark;

    &:visited {
        border-bottom-color: $color-inline-link--visited--dark;
        color: $color-inline-link--visited--dark;
    }

    &:focus {
        color: $color-inline-link--dark;
        border-bottom-color: $color-inline-link--dark;
        outline-color: $color-outline--dark;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: $color-inline-link--dark;
            background: $color-inline-link--dark;
            color: $color-text--dark;
        }
    }

    &:active {
        background-color: $color-inline-link--dark;
        border-bottom-color: $color-inline-link--dark;
        color: $color-text--dark;
        outline-color: $color-outline--dark;
    }
}

@mixin anchor-inline--gray() {
    @include anchor-inline();
    border-bottom: $color-gray12 $border-style-solid $border-width-default;
    color: $color-gray12;
    background-image: none;

    &:visited {
        border-bottom: $color-gray12 $border-style-solid $border-width-default;
        color: $color-gray12;
    }

    &:focus {
        color: $color-gray12;
        border-bottom: $color-gray12 $border-style-solid $border-width-default;
        outline-color: $color-purewhite;
    }

    &:hover {
        @include media('hover') {
            border-bottom: $color-purewhite $border-style-solid
                $border-width-default;
            color: $color-purewhite;
            background-image: none;
        }
    }

    &:active {
        background-color: transparent;
        border-bottom: $color-purewhite $border-style-solid
            $border-width-default;
        color: $color-purewhite;
        outline-color: $color-purewhite;
    }
}

@mixin anchor-inline--gray4() {
    @include anchor-inline--gray();

    color: $color-gray4;
    border-bottom-color: $color-gray4;

    &:visited {
        border-bottom-color: $color-gray4;
        color: $color-gray4;
    }

    &:focus {
        color: $color-gray4;
        border-bottom-color: $color-gray4;
    }

    &:hover {
        @include media('hover') {
            color: $color-purewhite;
            border-color: $color-purewhite;
            background-image: none;
        }
    }

    &:active {
        color: $color-purewhite;
        border-color: $color-purewhite;
        background-image: none;
    }
}

@mixin anchor-border-bottom(
    $border-color: $color-text,
    $border-style: $border-style-solid,
    $border-width: $border-width-default
) {
    border-bottom-color: $border-color;
    border-bottom-style: $border-style;
    border-bottom-width: $border-width;
}

@mixin anchor-discreet-before {
    &:before {
        background: $color-breadcrumb-link;
        bottom: -#{$border-width-default};
        content: '';
        display: block;
        height: $border-width-default;
        left: 0;
        opacity: 0;
        position: absolute;
        transition: opacity $transition-duration $transition-function,
            width $transition-duration--long $transition-function;

        width: 0;

        @include media('reducemotion') {
            transition: none;
        }
    }
}

///
/// anchor style for breadcrumbs, figcaption, etc.
///
@mixin anchor-discreet() {
    @include anchor-reset();
    @include anchor-default-outline--dark();
    @include anchor-border-bottom();

    color: $color-text;
    position: relative;
    background-image: none;

    @include anchor-discreet-before;

    &:before {
        transition: opacity $transition-duration $transition-function,
            width $transition-duration--long $transition-function;
    }

    &:focus {
        color: $color-text;
        @include anchor-border-bottom;
    }

    &:visited {
        color: $color-text;
        @include anchor-border-bottom;
    }

    &:hover {
        @include media('hover') {
            @include anchor-border-bottom;
            color: $color-text;
            background-image: none;

            &:before {
                opacity: 1;
                width: 100%;
                transition: opacity $transition-duration $transition-function,
                    width $transition-duration--long $transition-function;
            }
        }
    }

    &:active {
        @include anchor-border-bottom($color-breadcrumb-link);
    }
}

@mixin anchor-discreet--dark() {
    @include anchor-discreet();

    border-bottom-color: $color-text--dark;
    color: $color-text--dark;

    &:focus {
        color: $color-text--dark;
        border-bottom-color: $color-text--dark;
        outline-color: $color-outline--dark;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: $color-text--dark;
            color: $color-text--dark;
        }
    }

    &:active {
        border-bottom-color: $color-breadcrumb-link--dark;
        outline-color: $color-outline--dark;
        color: $color-text--dark;
    }
}

///
/// Only show focus/active state on images
///
@mixin anchor-img() {
    @include anchor-reset();

    &:focus {
        @include outline-properties();
    }

    &:active {
        @include outline-properties();
    }
}

@mixin anchor-img--dark() {
    @include anchor-img();

    &:focus {
        outline-color: $color-outline--dark;
    }

    &:active {
        outline-color: $color-outline--dark;
    }
}

@mixin anchor-pagination() {
    @include anchor-reset();
    background-color: $color-bg;
    background-image: linear-gradient(
        120deg,
        $color-red50 0%,
        $color-red50 100%
    );
    background-position: 0 100%;
    background-repeat: no-repeat;
    background-size: 100% 0;
    @include anchor-border-bottom($color-red50);
    border-left-color: $color-gray4;
    border-left-style: $border-style-solid;
    border-left-width: $border-width-hairline;
    border-right-color: $color-gray4;
    border-right-style: $border-style-solid;
    border-right-width: $border-width-hairline;
    border-top-color: $color-gray4;
    border-top-style: $border-style-solid;
    border-top-width: $border-width-hairline;
    color: $color-text;
    font-weight: $font-weight-text;
    transition: background-size $transition-duration $transition-function;

    @include media('reducemotion') {
        transition: none;
    }

    &:focus {
        background-color: $color-bg;
        @include anchor-border-bottom($color-red50);
        color: $color-text;
        font-weight: $font-weight-bold;
        @include outline-properties();
    }

    &:hover {
        @include media('hover') {
            background-size: 100% 100%;
            color: $color-text--dark;
            font-weight: $font-weight-text;
        }
    }

    &:active {
        background-size: 100% 100%;
        @include anchor-border-bottom($color-red50);
        color: $color-text--dark;
        font-weight: $font-weight-bold;
        @include outline-properties();
    }

    &:disabled {
        background-color: transparent;
        background-size: 100% 0;

        @include anchor-border-bottom(
            $color-gray4,
            $border-style-solid,
            $border-width-hairline
        );

        color: $color-gray7;
        cursor: default;
        font-weight: $font-weight-text;

        span {
            display: inline-block;
            padding-bottom: $border-width-hairline;
        }
    }

    &[aria-current] {
        background-color: transparent;
        @include anchor-border-bottom(
            $color-gray4,
            $border-style-solid,
            $border-width-hairline
        );

        color: $color-gray7;
        cursor: default;
        font-weight: $font-weight-bold;
        outline-color: transparent;
        outline-offset: 0;
        outline-style: none;
        outline-width: 0;
    }
}

@mixin anchor-pagination--dark() {
    @include anchor-pagination();
    background-color: $color-bg--dark;
    border-left-color: $color-gray8;
    border-right-color: $color-gray8;
    border-top-color: $color-gray8;
    color: $color-text--dark;

    &:focus {
        background-color: $color-bg--dark;
        color: $color-text--dark;
        outline-color: $color-text--dark;
    }

    &:hover {
        @include media('hover') {
            color: $color-text;
        }
    }

    &:active {
        background-color: $color-bg--dark;
        color: $color-text;
        outline-color: $color-text--dark;
    }

    &:disabled {
        border-bottom-color: $color-gray8;
        color: $color-gray5;
    }

    &[aria-current] {
        border-bottom-color: $color-gray8;
        color: $color-gray5;
    }
}

@mixin anchor-border-reset {
    border: none;
    border-bottom-width: 0;
    border-bottom-color: transparent;
    border-bottom-style: none;
}

/* ghost animation - underline animation used on tab buttons */
@-webkit-keyframes slide-left-in {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: none;
    }
}

@keyframes slide-left-in {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: none;
    }
}

@-webkit-keyframes slide-right-out {
    from {
        transform: none;
    }
    to {
        transform: translateX(100%);
    }
}

@keyframes slide-right-out {
    from {
        transform: none;
    }
    to {
        transform: translateX(100%);
    }
}

@mixin ghost-out() {
    transform: translateX(105%);
    -webkit-animation: slide-right-out 0.2s ease 0s both normal;
    animation: slide-right-out 0.2s ease 0s both normal;
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    opacity: 0;
    background-color: $color-gray3;
    width: 100%;
    height: $measure--half;
    transition: opacity 0.2s ease 0.2s;
    z-index: 0;
}
@mixin ghost-in() {
    transform: translateX(-105%);
    -webkit-animation: slide-left-in 0.2s ease 0s both normal;
    animation: slide-left-in 0.2s ease 0s both normal;
    opacity: 1;
    transition: none;
}
/* End of ghost animation */

@mixin cta-like-anchors {
    @include cta-primary-arrow-light-bg($color-purewhite, $color-red50);
    @include h5style();

    border-radius: 0;
    border: $border-width-default $border-style-solid $color-red50;
    box-shadow: none;
    height: 100%;
    margin-top: 0;
    width: 100%;

    &:after {
        align-items: center;
        display: flex;
        position: relative;
        right: $measure--half;
    }

    span {
        color: $color-gray11;
        margin: auto 0;
        width: 100%;
        &:active {
            color: $color-purewhite;
        }
    }

    &:visited {
        border: $border-width-default $border-style-solid $color-red50;
    }

    &:focus {
        border: $border-width-default $border-style-solid $color-red50;
        span {
            color: $color-gray11;
        }
        &:after {
            color: $color-gray11;
        }
    }

    &:active {
        background: $color-red50;
    }

    &:hover {
        @include media('hover') {
            span {
                color: $color-purewhite;
            }
            &:after {
                color: $color-purewhite;
            }
        }
    }
}

@mixin underline-transition(
    $inactive-color: $color-gray7,
    $hover-color: $color-gray10,
    $underline-width: $border-width-thick
) {
    @include anchor-border-reset();
    color: $inactive-color;
    background-image: linear-gradient($inactive-color, $inactive-color);
    background-position: 0% 100%;
    background-size: 0% $underline-width;
    background-repeat: no-repeat;
    transition: background-size $transition-duration--long ease-in;
    padding-bottom: $border-width-default;

    &:visited {
        @include anchor-border-reset();
        color: $inactive-color;
    }

    &:focus {
        @include anchor-border-reset();
        color: $inactive-color;
        background-size: 100% $underline-width;
    }

    &:hover {
        @include media('hover') {
            @include anchor-border-reset();
            color: $hover-color;
            background-image: linear-gradient($hover-color, $hover-color);
            background-size: 100% $underline-width;
        }
    }
}

// @mixin underline-transition--title targets HTML elements with class name 'underline-transition__title' within an
// anchor element to give it the same effect as @mixin underline-transition while the element is not an anchor
@mixin underline-transition--title(
    $inactive-color: $color-gray7,
    $hover-color: $color-gray10,
    $underline-width: $border-width-thick
) {
    .underline-transition__title {
        @include anchor-border-reset();
        background-image: linear-gradient($inactive-color, $inactive-color);
        background-position: 0% 100%;
        background-repeat: no-repeat;
        background-size: 0% $underline-width;
        transition: background-size $transition-duration--long ease-in;
        display: inline;
        padding-bottom: $border-width-default;
    }

    &:visited {
        .underline-transition__title {
            @include anchor-border-reset();
            color: $inactive-color;
        }
    }

    &:focus {
        .underline-transition__title {
            @include anchor-border-reset();
            color: $inactive-color;
            background-size: 100% $underline-width;
        }
    }

    &:hover {
        @include media('hover') {
            .underline-transition__title {
                @include anchor-border-reset();
                color: $hover-color;
                background-image: linear-gradient($hover-color, $hover-color);
                background-size: 100% $underline-width;
            }
        }
    }
}
