////
/// Temporary mixins
/// @group abstracts
////

///
/// menu is a temporary style for the temporary menu
///
@mixin anchor-menu() {
    @include anchor-discreet();
    border-bottom-color: transparent;

    &:focus {
        border-bottom-color: transparent;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: transparent;
        }
    }
}

@mixin anchor-menu--dark() {
    @include anchor-discreet--dark();
    border-bottom-color: transparent;

    &:focus {
        border-bottom-color: transparent;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: transparent;
        }
    }
}

@mixin anchor-button() {
    @include anchor-reset();
    color: $color-inline-link;
    border: none;

    &:focus {
        color: $color-text;
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
    }

    &:hover {
        @include media('hover') {
            background-image: none;
            color: $color-text;
        }
    }

    &:active {
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }

    &[disabled] {
        color: $color-text;
        &:focus,
        &:hover,
        &:active {
            outline: none;
        }
    }
}

@mixin anchor-button--dark() {
    @include anchor-button();
    color: $color-inline-link--dark;

    &:focus {
        color: $color-text--dark;
        outline-color: $color-outline--dark;
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;
        }
    }

    &:active {
        outline-color: $color-outline--dark;
    }

    &[disabled] {
        color: $color-text--dark;
        &:focus,
        &:hover,
        &:active {
            outline: none;
        }
    }
}

@mixin anchor-span() {
    @include anchor-reset();
    @include anchor-hover-background();
    color: $color-inline-link;

    span {
        border-bottom-color: $color-inline-link;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    &:focus {
        color: $color-inline-link;

        span {
            outline-color: $color-outline;
            outline-offset: $border-width-thick;
            outline-style: $border-style-dotted;
            outline-width: $border-width-default;
        }
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;

            span {
                outline: none;
            }
        }
    }

    &:active {
        background-color: $color-inline-link;
        color: $color-text--dark;

        span {
            outline-color: $color-outline--dark;
            outline-offset: $border-width-thick;
            outline-style: $border-style-dotted;
            outline-width: $border-width-default;
        }
    }
}

@mixin anchor-span--dark() {
    @include anchor-span();
    @include anchor-hover-background--dark();

    color: $color-inline-link--dark;

    span {
        border-bottom-color: $color-inline-link--dark;
    }

    &:focus {
        color: $color-inline-link--dark;

        span {
            outline-color: $color-outline--dark;
        }
    }

    &:hover {
        @include media('hover') {
            color: $color-text;
        }
    }

    &:active {
        background-color: $color-inline-link--dark;
        color: $color-text;

        span {
            outline-color: $color-outline;
        }
    }
}

@mixin anchor-menu-button() {
    @include anchor-reset();
    color: $color-inline-link;
    border: none;

    &:focus {
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
    }

    &:hover {
        @include media('hover') {
            color: $color-text;
        }
    }

    &:active {
        outline-color: $color-outline;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }
}

@mixin anchor-menu-button--dark() {
    @include anchor-menu-button();
    color: $color-inline-link--dark;

    &:focus {
        color: $color-text--dark;
        outline-color: $color-outline--dark;
    }

    &:hover {
        @include media('hover') {
            color: $color-text--dark;
        }
    }

    &:active {
        outline-color: $color-outline--dark;
    }
}
