.feedback {
    @include media('print') {
        display: none;
    }

    @include alphatype();

    display: grid;
    grid-area: feedback;
    grid-template-areas:
        'direct'
        'formlink';
    grid-template-columns: auto;
    grid-template-rows: auto auto;

    @include media('>mobile') {
        grid-template-areas: 'direct formlink';
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
    }

    .feedback__form-link {
        grid-area: formlink;
        text-align: left;

        @include media('>mobile') {
            text-align: right;
        }

        a {
            @include anchor-inline--notvisited();
        }
    }

    .feedback__direct {
        grid-area: direct;
        text-align: left;

        legend {
            @include alphatype();
            float: left; // unavoidable float because legend sucks
        }

        @include media('<=mobile') {
            margin-bottom: $measure--basic;
        }

        button {
            @include anchor-inline();
            margin-left: $measure--basic;
        }
    }
}
