////
/// Breadcrumbs
/// @group patterns
////
.breadcrumbs {
    @include standard-margins();
    @include body-baseline();
    display: grid;
    margin-top: $measure--quarter;

    a {
        @include anchor-discreet();
    }

    ol {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        width: 100%;

        li {
            @include smalltype();
            color: $color-text--caption;
            font-weight: $font-weight-text;
            line-height: $measure--small1;

            &:not(:last-of-type) {
                margin-right: $measure--half;
                margin-bottom: $measure--quarter;

                &:after {
                    @include font-family('icon');
                    font-size: $font-size--icon;
                    color: $color-text--caption;
                    content: '\f105';
                    font-weight: $font-weight-light;
                    margin-left: $measure--quarter;
                }
            }
        }
    }
}
