body.modal--in.no-scroll {
    position: fixed;
    width: 100%;

    .modal__background {
        opacity: 0.7;
    }
}

.modal__background {
    background: $color-nearblack;
    pointer-events: none;
    height: 100%;
    opacity: 0;
    top: 0;
    left: 0;
    position: fixed;
    transition: opacity 0.35s ease;
    width: 100%;
    z-index: 3;
}

.modal__popup {
    align-items: center;
    height: 100%;
    justify-content: center;
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    z-index: 4;
    opacity: 1;
    pointer-events: auto;
    display: flex;
}

.modal__popup[data-hidden='true'] {
    opacity: 0;
    pointer-events: none;
}

.modal__content {
    background: $color-purewhite;
    border: none;
    overflow-y: auto;
    padding: $measure--small2 $measure--medium1 $measure--medium1;
    position: relative;
}

.modal__heading {
    @include standfirst;
    font-size: $font-size--gamma;
    font-weight: $font-weight-medium;
}

.modal_strapline {
    color: $color-red50;
}

.modal__close {
    color: $color-gray7;
    padding: $measure--threequarters;
    position: absolute;
    right: 1px;
    top: 1px;

    @include anchor-default-outline--dark;

    &:hover {
        @include media('hover') {
            background-image: none;
            border: none;
            background-color: $color-gray1;
            color: $color-gray10;
        }

        &:before {
            font-size: $font-size--icon + 0.2;
        }
    }

    &:active {
        background-color: $color-gray1;
        color: $color-gray10;
    }

    &:focus {
        color: $color-gray10;
    }

    &:active,
    &:focus {
        outline-offset: 0;
    }
}

.modal__body-copy {
    margin-top: $measure--basic;
}

.modal__buttons--calendar {
    a {
        @include anchor-reset-properties;
        @include anchor-default-outline--dark;
        border: $border-width-hairline $border-style-solid $color-gray3;
        border-bottom-color: $color-gray3;
        border-bottom-width: $border-width-hairline;
        color: $color-gray8;
        display: block;
        font-size: $font-size--alpha;
        margin-top: $measure--small4;
        padding: $measure--threequarters;
        position: relative;

        &:visited {
            border-bottom-color: $color-gray3;
            color: $color-gray8;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                color: $color-purewhite;
                border: $border-width-hairline $border-style-solid $color-pink90;
                border-bottom-color: $color-pink90;
                border-bottom-width: $border-width-hairline;
                background-color: $color-pink90;
                background-image: none;
                background-size: initial;

                span:before {
                    content: '\f271';
                }
            }
        }

        &:focus {
            background-color: $color-gray1;
            border: $border-width-hairline $border-style-solid $color-gray4;
            border-bottom-color: $color-gray4;
            border-bottom-width: $border-width-hairline;
            color: $color-gray11;

            span:before {
                content: '\f274';
            }
        }

        &:active {
            background-color: $color-pink100;
            border: $border-width-hairline $border-style-solid $color-pink100;
            border-bottom-color: $color-pink100;
            border-bottom-width: $border-width-hairline;
            color: $color-purewhite;

            span:before {
                content: '\f274';
            }
        }
    }
}

.modal__buttons--slider {
    margin-top: $measure--medium1;

    button {
        @include anchor-default-outline--dark;
        padding: $measure--basic;
        border: $border-width-thick $border-style-solid $color-red50;
        width: 100%;
        display: flex;
        align-items: center;

        @include media('>tablet') {
            border: none;
            width: 50%;
        }

        &:first-child {
            position: relative;
            text-align: left;

            @include media('>tablet') {
                padding-left: 0;
            }

            &:after {
                @include media('>tablet') {
                    content: '';
                    height: 100%;
                    width: $border-width-default;
                    background: $color-red50;
                    position: absolute;
                    top: 0;
                    right: 0;
                }
            }

            .modal__buttons__icon {
                margin-right: $measure--threequarters;
            }
        }

        &:last-child {
            text-align: right;

            @include media('>tablet') {
                padding-right: 0;
            }

            .modal__buttons__icon {
                margin-left: $measure--threequarters;
            }
        }

        &:hover {
            .modal__underline-transition {
                span {
                    background-image: linear-gradient(
                        $color-red50,
                        $color-red50
                    );
                    background-size: 100% $border-width-default;
                }
            }
        }

        .modal__underline-transition {
            @include anchor-border-bottom;

            span {
                position: relative;
                background-image: linear-gradient(
                    $color-nearblack,
                    $color-nearblack
                );
                background-position: 0% 100%;
                background-repeat: no-repeat;
                background-size: 0% $border-width-default;
                transition: background-size $transition-duration--long ease-in;
                padding-bottom: $border-width-default;
                line-height: 1.5;
            }
        }
    }
}

.modal__buttons {
    max-width: 55ch;

    @include media('>tablet') {
        display: flex;
    }
}

.no-click {
    pointer-events: none;
}

.no-js {
    .modal__popup {
        position: initial;
    }

    .modal__popup--hidden {
        display: block;
        opacity: 1;
    }

    .modal__content {
        max-height: none;
        opacity: 1;
        padding: 0;
        pointer-events: auto;
    }
}
