.theme--dark {
    @include media('screen') {
        background-color: $color-bg--dark;
        color: $color-text--dark;

        article {
            a {
                @include anchor-inline--dark();
            }
        }

        .skip-link:focus {
            background-color: $color-bg--dark;
        }

        .logo--light {
            display: block;
        }

        .logo--dark {
            display: none;
        }
    }

    .form {
        input,
        textarea {
            &:not([type='submit']) {
                background-color: $color-shadow--dark;
                border-color: $color-bg--dark;
                color: $color-text--dark;
            }
        }

        input[type='submit'] {
            @include anchor-button--dark();
        }
    }

    @import '../patterns/header/dark';
    @import '../patterns/footer/dark';
    @import '../patterns/breadcrumbs/dark';
    @import '../patterns/cookie-notice/dark';
    @import '../patterns/cta-block/dark';
    @import '../patterns/feedback/dark';
    @import '../patterns/figure/dark';
    @import '../patterns/quotation/dark';

    &.js {
        @import '../patterns/accordion/dark';
        @import '../patterns/menu/dark';
        @import '../patterns/pagination/dark';
        @import '../patterns/tabs/dark';
        @import '../patterns/theme-switcher/dark';
    }
}
