@mixin tabs-style--dark() {
    .tabs__links {
        li {
            &:not(:first-of-type) {
                border-left-color: $color-bg--dark;
            }

            a {
                @include anchor-span--dark();

                &:not(:hover):not([aria-selected='true']) {
                    background-color: $color-shadow--dark;
                    border-bottom-color: $color-shadow--dark;
                }

                &[aria-selected='true'] {
                    border-left-color: $color-shadow--dark;
                    border-right-color: $color-shadow--dark;
                    border-top-color: $color-shadow--dark;
                }
            }
        }
    }

    .tabs__panel {
        border-left-color: $color-shadow--dark;
        border-right-color: $color-shadow--dark;
        border-bottom-color: $color-shadow--dark;
    }
}

@include media('screen', '>mobile') {
    @include tabs-style--dark();
}

@include media('screen', '<=mobile') {
    @include tabs-style--dark();
}
