.lifelong-learning {
    .banner-text {
        background-color: $color-pink80;
        color: $color-purewhite;
    }

    .banner__overlay {
        background: linear-gradient(to top, $color-pink80, transparent 100%);
    }

    .wrapper--key-information--lifelong-learning {
        background-color: $color-gray1;
        padding-bottom: $measure--small4;
    }

    .key-information--lifelong-learning {
        @include standard-grid();
        @include standard-margins();

        grid-template-rows: repeat(2, auto);

        @include media('<=mobile') {
            grid-template-areas:
                'heading heading heading heading heading heading'
                'dates dates dates dates dates dates'
                'controls controls controls controls controls controls';
        }

        @include media('>mobile', '<=between') {
            grid-template-areas:
                'heading heading heading heading heading heading heading heading heading heading heading heading'
                'dates dates dates dates dates dates dates dates dates dates dates dates'
                'controls controls controls controls controls controls controls controls controls controls controls controls';
        }

        @include media('>between') {
            grid-template-areas:
                'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                'dates dates dates dates dates dates dates dates dates dates dates dates dates dates dates dates dates dates'
                'controls controls controls controls controls controls controls controls controls controls controls controls controls controls controls controls controls controls';
        }

        & > h2 {
            color: $color-pink80;
            grid-area: heading;
        }

        .key-information--lifelong-learning__icon {
            margin-right: $measure--basic;
        }

        & > ul,
        & > .pagination__wrapper {
            grid-area: dates;
            max-width: unset;
        }

        & > .key-information--lifelong-learning__controls {
            grid-area: controls;
        }
        & > ul > li {
            background-color: $color-purewhite;
            border: $border-width-hairline $border-style-solid $color-gray3;
            padding: 0 $measure--basic $measure--basic $measure--basic;
        }
        & > ul > li > ul {
            margin-left: 0;
            margin-right: 0;
        }
    }

    .lifelong-learning__start-date__cta {
        text-align: center;

        .cta-block__cta > span {
            @include smalltype();
            display: block;
        }
    }

    .lifelong-learning__tutors {
        @include standard-grid();
        @include standard-margins();
        grid-template-rows: repeat(2, auto);
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;

        @include media('<=mobile') {
            grid-template-areas:
                'heading heading heading heading heading heading'
                'related related related related related related';
        }

        @include media('>mobile', '<=between') {
            grid-template-areas:
                'heading heading heading heading heading heading heading heading heading heading heading heading'
                '. . related related related related related related related related . .';
        }

        @include media('>between') {
            grid-template-areas:
                'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                '. . . . . related related related related related related related related . . . . .';
        }

        h2 {
            @include heading--centered($color-pink80);
            grid-area: heading;
        }

        .related-content {
            display: flex;
            flex-direction: column;
            grid-area: related;
            justify-content: center;
        }
    }

    .lifelong-learning__testimonials {
        border-top: $border-width-default $border-style-solid $color-gray3;
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;

        .related-content {
            @include standard-margins();
            display: flex;
            justify-content: center;
        }

        h2 {
            @include heading--centered($color-pink80);
        }
    }

    .lifelong-learning__related-courses {
        @include standard-margins();
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;

        .related-content {
            display: grid;
            margin-top: $measure--basic;
            grid-template-columns: auto auto;

            @include media('<between') {
                display: block;
            }
        }

        h2 {
            @include heading--centered($color-pink80);
        }
    }

    .lifelong-learning__video {
        @include standard-margins();
        background: $color-red5;
        padding: $measure--small4;
    }

    .embedded-video {
        padding-top: 0;
        padding-bottom: 0;
    }

    @include media('>=tablet') {
        .tabs .tabs__panel {
            @include standard-margins();
            border: $border-width-default $border-style-solid $color-gray3;
        }
    }
}

.key-information--lifelong-learning--bespoke {
    @include standard-margins();
    padding-top: $measure--small4;

    .key-information--lifelong-learning--bespoke__contact {
        @include standard-grid();
        grid-template-rows: repeat(2, auto);

        @include media('<=mobile') {
            grid-template-rows: repeat(4, auto);
            grid-template-areas:
                'header header header header header header'
                'location location location location location location'
                'contact contact contact contact contact contact'
                'links links links links links links';
        }

        @include media('>mobile', '<=tablet') {
            grid-template-rows: repeat(3, auto);
            grid-template-areas:
                'header header header header header header header header header header header header'
                'location location location location location location location location location location location location'
                'contact contact contact contact contact contact contact contact contact contact contact contact'
                'links links links links links links links links links links links links';
        }

        @include media('>tablet', '<=between') {
            grid-template-areas:
                'header header header header header header header header header header header header'
                'location location location location contact contact contact contact links links links links';
        }

        @include media('>between') {
            grid-template-areas:
                'header header header header header header header header header header header header header header header header header header'
                'location location location location location location contact contact contact contact contact contact links links links links links links';
        }

        .key-information--lifelong-learning--bespoke__contact__header {
            color: $color-gray11;
            grid-area: header;
        }

        .key-information--lifelong-learning--bespoke__contact__location {
            grid-area: location;

            .icon {
                color: $color-pink80;
            }
        }

        .key-information--lifelong-learning--bespoke__contact__details {
            grid-area: contact;
            list-style: none;
            margin-left: 0;

            .icon {
                color: $color-pink80;
            }
        }

        .key-information--lifelong-learning--bespoke__contact__related-links {
            align-items: center;
            display: flex;
            flex-direction: column;
            grid-area: links;
            max-width: unset;

            .cta-block__cta {
                @include media('<=between') {
                    display: flex;
                    flex-direction: column;
                    margin-top: $measure--small4;
                }

                a,
                button {
                    @include cta-basic-light-bg(
                        $color-pink80,
                        $color-purewhite
                    );

                    @include media('<=between') {
                        justify-content: center;
                    }
                }
            }
        }
    }
}

.wrapper--lifelong-learning__introduction {
    background-color: $color-pink80;
    color: $color-purewhite;
}

.lifelong-learning__introduction {
    @include standard-margins();
    padding-top: $measure--small4;
    padding-bottom: $measure--small4;
}

.lifelong-learning__content + .lifelong-learning__contact {
    margin-top: $measure--small4;
}

.lifelong-learning__contact {
    @include standard-margins();
    background-color: $color-pink90;
    color: $color-purewhite;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-bottom: $measure--small4;
    padding-left: $measure--small4;
    padding-right: $measure--small4;
    padding-top: $measure--small4;
    margin-bottom: $measure--small4;

    @include media('>tablet') {
        flex-direction: row;
    }

    .lifelong-learning__contact__heading {
        margin-top: 0;
        text-align: center;

        @include media('>tablet') {
            align-self: center;
            margin-right: $measure--small4;
            text-align: left;
        }
    }

    .cta-block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: unset;

        @include media('>tablet') {
            flex-direction: row;
        }

        li {
            align-self: stretch;
            margin-top: $measure--basic;

            @include media('>tablet') {
                margin-top: 0;
            }

            a {
                @include cta-basic-light-bg($color-gray3, $color-text);

                align-self: stretch;
                display: flex;
                justify-content: center;

                &:focus,
                &:active {
                    @include anchor-default-outline--bright();
                }
            }

            &:not(:last-of-type) {
                @include media('>tablet') {
                    margin-right: $measure--small4;
                }
            }
        }
    }
}

.lifelong-learning--bespoke__introduction {
    @include standard-margins();
    padding-top: $measure--small4;
}

@mixin display-tabs-content() {
    @include standard-margins();
    padding-top: $measure--small4;

    .tabs__links {
        display: none;
    }

    .tabs__panel {
        background-color: $color-purewhite;
        border-top: 0;
        padding: 0;

        &[hidden] {
            display: block;
        }

        &:not([hidden]) {
            .tabs__panel__content {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }

    .tabs__panel__content {
        @include standard-grid();
        grid-template-rows: repeat(2, auto);

        @include media('<=mobile') {
            grid-template-areas:
                'heading heading heading heading heading heading'
                'content content content content content content';
        }

        @include media('>mobile') {
            grid-template-areas:
                'heading heading heading heading heading heading heading heading heading heading heading heading'
                'content content content content content content content content content content content content';
        }

        @include media('>between') {
            grid-template-areas:
                '. . . . . heading heading heading heading heading heading heading heading . . . . .'
                '. . . . . content content content content content content content content . . . . .';
        }

        .tabs__panel__heading {
            @include heading--centered($color-pink80);
            grid-area: heading;
        }

        .tabs__panel__body {
            grid-area: content;
        }
    }
}

@include media('screen') {
    .js .lifelong-learning .modal__content,
    .js .lifelong-learning .modal__content a:not(:hover):not(:active) {
        color: $color-text;
    }

    .js .lifelong-learning .paginated-list .page ul,
    .js .lifelong-learning .paginated-list .page ol {
        list-style-type: none;
        margin-left: 0;
    }

    .js .lifelong-learning .modal__trigger {
        @include cta-basic-light-bg($color-red50, $color-purewhite);
        padding: $measure--threequarters $measure--small2;
    }

    .js .lifelong-learning__content .tabs__links {
        @include edge-to-edge-margins();
    }

    .js .lifelong-learning__content .accordion {
        @include standard-margins();
    }

    .js .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
        @include display-tabs-content();
    }

    .js .lifelong-learning--bespoke .tabs {
        @include media('<tablet') {
            @include display-tabs-content();
        }
    }

    .js .lifelong-learning__content .tabs__panel:not([hidden]) {
        .tabs__panel__content {
            @include standard-grid();
            @include standard-margins();
            grid-template-rows: repeat(2, auto);

            @include media('<=mobile') {
                grid-template-areas:
                    'heading heading heading heading heading heading'
                    'body body body body body body';
            }

            @include media('>mobile', '<=between') {
                grid-template-areas:
                    '. . heading heading heading heading heading heading heading heading . .'
                    '. . body body body body body body body body . .';
            }

            @include media('>between') {
                grid-template-areas:
                    '. . . . . heading heading heading heading heading heading heading heading . . . . .'
                    '. . . . . body body body body body body body body . . . . .';
            }

            .tabs__panel__heading {
                grid-area: heading;
            }

            .tabs__panel__body {
                grid-area: body;
            }
        }
    }
}

.js .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
    > .tabs__panel {
        @include standard-grid();
        @include standard-margins();
        grid-template-rows: repeat(2, auto);

        @include media('<=mobile') {
            grid-template-areas:
                'heading heading heading heading heading heading'
                'body body body body body body';
        }

        @include media('>mobile', '<=between') {
            grid-template-areas:
                '. . heading heading heading heading heading heading heading heading . .'
                '. . body body body body body body body body . .';
        }

        @include media('>between') {
            grid-template-areas:
                '. . . . . heading heading heading heading heading heading heading heading . . . . .'
                '. . . . . body body body body body body body body . . . . .';
        }

        .tabs__panel__heading {
            grid-area: heading;
        }

        .tabs__panel__body {
            grid-area: body;
        }
    }
}

.lifelong-learning__selling-points {
    @include standard-margins();
    padding: $measure--small2 $measure--basic;
    border: $border-style-solid $border-width-hairline $color-gray3;
    background-color: $color-purewhite;

    @include media('>between') {
        padding: $measure--medium5 $measure--small2;
        grid-gap: $measure--medium7;
        display: grid;
        grid-template-columns: auto auto auto;
    }
}

.lifelong-learning__selling-points__background {
    background: linear-gradient(180deg, $color-pink80 50%, $color-gray1 50%);
    background-repeat: no-repeat;
    padding-bottom: $measure--small3;
}

.lifelong-learning__selling-points__card {
    display: flex;
    margin-bottom: $measure--small4;

    &:last-child {
        margin-bottom: 0;
    }

    @include media('>between') {
        margin-bottom: 0;
    }

    .icon {
        font-size: $measure--small5;
        min-width: $measure--medium3;
        margin-right: $measure--small4;

        @include media('>mobile') {
            min-width: $measure--medium2;
        }

        @include media('>between') {
            margin-right: $measure--basic;
        }
    }

    &:nth-child(1) {
        .icon {
            color: $color-green50;
        }
    }

    &:nth-child(2) {
        .icon {
            color: $color-teal50;
        }
    }

    &:nth-child(3) {
        .icon {
            color: $color-purple50;
        }
    }
}

.lifelong-learning__selling-points__title {
    font-weight: $font-weight-bold;
}

.lifelong-learning {
    .slide__controls__progress__active {
        color: $color-pink90;
    }
}

.lifelong-learning {
    .slide__controls__progress__active {
        color: $color-pink90;
    }

    .swiper-slider-arrow {
        @include control-button($color-pink90);
    }
}
