.embedded-video {
    display: grid;
    grid-template-columns: repeat(18, 1fr);
    padding-bottom: $measure--medium4;
    padding-top: $measure--medium4;

    @include media('screen', '<=between') {
        grid-template-columns: repeat(12, 1fr);
        padding-bottom: $measure--medium1;
        padding-top: $measure--medium1;
    }

    @include media('screen', '<=mobile') {
        grid-template-columns: repeat(6, 1fr);
        padding-bottom: $measure--medium1;
        padding-top: $measure--medium1;
    }
}

.embedded-video .youtube-video__align-right {
    grid-column: 8 / 19;
    margin-top: 0;

    @include media('screen', '<=between') {
        margin-top: $measure--small1;
    }
}

.embedded-video-details {
    align-items: center;
    display: flex;
    grid-column: 1 / 8;
}

.embedded-video-details__heading {
    margin-top: 0;
}
.embedded-video-details__seperator {
    background: $color-pink80;
    display: block;
    height: $measure--quarter;
    margin-bottom: $measure--small4;
    margin-top: $measure--small4;
    width: $measure--large2;

    @include media('screen', '<=mobile') {
        margin-bottom: $measure--small1;
        margin-top: $measure--small1;
    }
}

.youtube-video {
    grid-column: 1 / -1;

    @include media('screen', '>between') {
        padding-left: $measure--small2;
    }
}
.youtube-video__wrapper {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    width: 100%;

    iframe {
        height: 100%;
        left: 0;
        margin: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
}

.youtube-video__caption-element--text,
.youtube-video__caption-element--time {
    display: block;
}

@include media('screen', '<=between') {
    .embedded-video .youtube-video__align-right,
    .embedded-video-details {
        grid-column: 3 / 11;
    }

    .youtube-video {
        grid-column: 1 / -1;
    }
}

@include media('screen', '<=mobile') {
    .embedded-video .youtube-video__align-right,
    .embedded-video-details {
        grid-column: 1 / -1;
    }
}
