@include media('screen') {
    .menu {
        color: $color-text--caption--dark;

        li {
            &.menu__hierarchy a {
                span {
                    border-bottom-color: $color-inline-link--dark;
                }
            }

            &.menu__current span {
                span {
                    border-bottom-color: $color-text--dark;
                }
            }

            &.menu__current > span:focus {
                outline-color: $color-text--dark;
            }

            a {
                @include anchor-menu--dark();
            }

            &.menu__hierarchy > a {
                span {
                    border-bottom-width: $border-width-default;
                    border-bottom-style: $border-style-solid;
                    border-bottom-color: $color-text--dark;
                }
            }
        }

        .menu__button {
            button {
                @include anchor-button--dark();
            }
        }

        &[data-open='true'] {
            .menu__columns {
                background-color: $color-bg--dark;
            }

            .menu__columns__col > ul {
                &::-webkit-scrollbar-thumb {
                    border-color: $color-bg--dark;
                    background-color: rgba(255, 255, 255, 0.5);
                }

                &::-webkit-scrollbar-track {
                    background-color: $color-bg--dark;
                }

                button {
                    @include anchor-menu-button--dark();

                    &[aria-expanded='true'] {
                        .menu__button__icon:before {
                            border-bottom-color: $color-inline-link--dark;
                        }
                    }

                    &:hover,
                    &:focus {
                        .menu__button__icon:before {
                            border-bottom-color: $color-text--dark;
                        }
                    }
                }
            }
        }
    }
}
