////
/// CMS editor warning
/// @group patterns
////
.cms-editor-warning {
    display: none;
}

@mixin cms-warning() {
    background-color: $color-red80;
    color: $color-text--dark;
    padding-left: $measure--half;
    padding-right: $measure--half;
}

.cms-editor {
    div.cms-editor-warning {
        display: block;

        p {
            @include cms-warning();
            @include alphatype();
            margin-bottom: $measure--half;
            max-width: $max-line-length;
            padding-top: $measure--half;
            padding-bottom: $measure--medium3;
            position: relative;

            &:after {
                @include extrasmalltype();
                bottom: $measure--half;
                content: 'CMS editor warning';
                font-weight: $font-weight-light;
                position: absolute;
                right: $measure--half;
            }
        }
    }

    span.cms-editor-warning {
        @include cms-warning();
        display: inline;
    }

    .cms-editor-warning-remover {
        @include extrasmalltype();
        @include anchor-inline();
        position: absolute;
        right: 0;
        margin-bottom: $measure--basic;
    }
}
