////
/// Page
/// @group base
////
html {
    background-color: $color-bg;
    color: $color-text;
    box-sizing: border-box;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;

    @include media('reducemotion') {
        scroll-behavior: auto;
    }
}

body {
    min-height: 100vh;
    position: relative;
    scroll-behavior: smooth;

    @include media('reducemotion') {
        scroll-behavior: auto;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.skip-link:focus {
    background-color: $color-bg;
    outline-offset: 0;
    padding: $measure--basic;
    position: sticky;
    top: $measure--basic;
}

.logo--light {
    display: none;
    width: $measure--large2;
}

.logo--dark {
    display: block;
    width: $measure--large2;
}

@mixin edge-to-edge-margins() {
    max-width: $width--max;

    @include media('>large') {
        margin-left: auto;
        margin-right: auto;
    }
}

@mixin default-margins() {
    @include media('<=large') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

@mixin standard-margins() {
    @include edge-to-edge-margins();
    @include default-margins();
}
