.banner-standfirst-label {
    .banner__image {
        overflow: hidden;
    }
    .banner__block-color {
        height: $measure--huge1;
        margin-top: $measure--basic;
        background: $color-gray2;
        grid-column: 1 / -1;

        @include media('<tablet') {
            height: $measure--large1;
        }
    }

    .banner__standfirst {
        background: $color-purewhite;
        color: $color-nearblack;
        text-align: center;
        opacity: 1;
        grid-column: 1 / -1;
        bottom: 0;

        @include media('>tablet') {
            grid-column: 2 / 12;
            bottom: $measure--medium9;
        }

        @include media('>between') {
            grid-column: 2 / 18;
            bottom: $measure--medium9;
        }

        @include media('>desktop') {
            grid-column: 3 / 17;
        }
    }

    .labels {
        color: $color-gray7;
        margin-bottom: $measure--small2;
        font-weight: $font-weight-bold;
        @include smalltype();
    }

    .standfirst {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
}
