.card--contact {
    background: $color-purewhite;
    @include box-shadow();
    padding: $measure--small1;

    &:last-child {
        @include media('<tablet') {
            margin-top: $measure--basic;
        }
    }

    .card--contact__header {
        @include betatype;
        font-weight: $font-weight-medium;
        margin-top: 0;
        color: $color-gray7;
        border-bottom: $border-width-hairline $border-style-solid $color-gray2;
        padding-bottom: $measure--half;
    }
}

.card--contact__name,
.card--contact__position,
.card--contact__key-info {
    @include alphatype;
}

.card--contact__name {
    margin-top: $measure--small2;
    font-weight: $font-weight-medium;
}

.card--contact__key-info {
    display: flex;
    align-items: center;
    word-break: break-word;
    margin-top: $measure--small2;

    .icon {
        color: $color-red50;
        margin-right: $measure--half;
    }
}
