.banner {
    position: relative;
    overflow: hidden;
    @include standard-grid();

    @include media('<=mobile') {
        grid-template-areas: 'b b b b b b';
    }

    @include media('>mobile') {
        grid-template-areas: 'b b b b b b b b b b b b';
    }

    @include media('>between') {
        grid-template-areas: 'b b b b b b b b b b b b b b b b b b';
    }
}

.banner__image {
    height: 100%;
    grid-area: b;

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>=between') {
        max-height: $banner-between-image-max-height;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

.banner__overlay {
    background: linear-gradient(to top, $color-gray9, transparent 100%);
    grid-area: b;
    width: 100%;
    height: 100%;
}

.banner__overlay__heading {
    @include text-shadow();
    @include standard-margins();

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
    color: $color-purewhite;

    @include media('<=between') {
        padding-bottom: $measure--basic;
    }
    padding-bottom: $measure--small4;
}
.banner--center {
    text-align: center;

    p {
        max-width: $width--max;
    }
}
