////
/// Footer
/// @group patterns
////
.global-footer {
    @include standard-grid();
    @include body-baseline();
    @include standard-margins();

    border-top: $border-width-hairline $border-style-solid $color-text;
    grid-row-gap: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;

    grid-template-rows: repeat(4, auto);
    grid-template-areas:
        'feedback feedback feedback feedback feedback feedback'
        'backtotop backtotop backtotop backtotop backtotop backtotop'
        'switcher switcher switcher switcher switcher switcher'
        'uol uol uol city city city'
        'copyright copyright copyright copyright copyright copyright';

    @include media('>mobile', '<=between') {
        grid-template-rows: repeat(3, auto);
        grid-template-areas:
            'feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback'
            'backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop'
            'switcher switcher switcher switcher . . . . . uol uol city'
            'copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright';
    }

    @include media('>between') {
        grid-template-rows: repeat(3, auto);
        grid-template-areas:
            'feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback feedback'
            'backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop backtotop'
            'switcher switcher switcher switcher switcher switcher . . . . . . . uol uol uol city city'
            'copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright copyright';
    }

    > .footer-logo {
        align-self: end;
        text-align: right;

        @include media('<=mobile') {
            text-align: center;
        }
    }

    > .footer-logo--uol {
        grid-area: uol;
    }

    > .footer-logo--city {
        grid-area: city;
    }

    > .copyright {
        @include extrasmalltype();

        font-weight: $font-weight-normal;
        grid-area: copyright;
        text-align: right;

        @include media('<=mobile') {
            text-align: center;
        }
    }

    > .theme-switcher {
        grid-area: switcher;
    }

    .footer-logo {
        a {
            @include anchor-img();

            display: inline-block;
        }
    }
}
