////
/// Global Header
/// @group patterns
////
.global-header {
    @include standard-grid();
    @include body-baseline();
    @include standard-margins();

    align-items: center;
    grid-template-areas: 'logo logo logo menu menu menu';
    padding-top: $measure--basic;
    padding-bottom: $measure--basic;
    position: relative;

    @include media('>mobile', '<=between') {
        grid-template-areas: 'logo logo menu menu menu menu menu menu menu menu menu menu';
    }

    @include media('>between') {
        grid-template-areas: 'logo logo logo menu menu menu menu menu menu menu menu menu menu menu menu menu menu menu';
    }

    .header-logo {
        align-self: start;
        grid-area: logo;

        a {
            @include anchor-img();

            display: inline-block;
        }
    }
}
