////
/// Menu
/// @group patterns
////
.menu {
    @include media('print') {
        display: none;
    }

    color: $color-text--caption;

    ul {
        list-style: none;
        padding-top: $measure--half;
        padding-bottom: $measure--half;
        text-align: left;
    }

    li {
        margin-left: $measure--half;

        a,
        &.menu__current > span {
            padding: $measure--quarter;
            border: none;
        }

        &.menu__current > span:focus {
            outline-color: $color-text;
            outline-width: $border-width-default;
            outline-style: $border-style-dotted;
        }

        a {
            @include anchor-menu();
        }

        &.menu__hierarchy > a {
            span {
                border-bottom-width: $border-width-default;
                border-bottom-style: $border-style-solid;
                border-bottom-color: $color-text;
            }
        }
    }
}

.no-js .menu {
    font-size: $font-size--extrasmalltype;
    grid-area: menu;

    .menu__button {
        display: none;
    }

    li {
        margin-top: $measure--half;
    }

    .menu__level3,
    .menu__level4,
    .menu__level5 {
        display: none;
        margin-left: $measure--basic;
    }

    .menu__current,
    .menu__hierarchy {
        & > .menu__level3,
        & > .menu__level4,
        & > .menu__level5 {
            display: block;
        }
    }
}

.js {
    .menu {
        grid-area: menu;
        text-align: right;
        z-index: 2;

        .menu__button {
            @include h1style();

            .menu__toggle {
                &:before {
                    content: '\f141';

                    @include media('<=between') {
                        content: '\f142';
                    }
                }
            }

            button {
                @include anchor-button();

                div {
                    display: grid;
                    grid-template-columns: auto auto;
                    align-items: center;
                }
            }
        }

        .menu__columns,
        ul {
            display: none;
        }

        &[data-open='true'] {
            .menu__toggle {
                &:before {
                    content: '\f00d';
                }
            }

            .menu__columns__col {
                margin-left: 0;
                padding-bottom: $measure--basic;
                padding-right: $measure--half;
            }

            .menu__columns__col > ul {
                display: block;
                max-height: 66vh;
                overflow-y: scroll;
                position: relative;

                &::-webkit-scrollbar {
                    -webkit-appearance: none;
                }

                &::-webkit-scrollbar:vertical {
                    width: 11px;
                }

                &::-webkit-scrollbar:horizontal {
                    height: 11px;
                }

                &::-webkit-scrollbar-thumb {
                    border-radius: 8px;
                    border: $border-width-default $border-style-solid $color-bg;
                    background-color: rgba(30, 30, 30, 0.5);
                }

                &::-webkit-scrollbar-track {
                    background-color: $color-bg;
                    border-radius: 8px;
                }

                @include media('<=between') {
                    width: 100%;
                }

                li {
                    display: grid;
                    grid-template-columns: 4fr 1fr;
                    grid-template-rows: auto;
                    grid-template-areas: 'link button';

                    @include media('<=between') {
                        grid-template-columns: 9fr 1fr;
                    }

                    a {
                        grid-area: link;
                    }

                    &[data-open='true'] {
                        > a > span {
                            font-weight: $font-weight-bold;
                        }

                        > span > span {
                            font-weight: $font-weight-bold;
                        }

                        & > ul {
                            @include media('<=between') {
                                display: block;
                            }
                        }
                    }
                }

                button {
                    @include anchor-menu-button();
                    margin-right: $measure--half;

                    .menu__button__icon {
                        &:before {
                            @include font-family('icon');
                            content: '\f105';
                            font-size: $font-size--beta;
                            font-weight: $font-weight-light;

                            @include media('<=between') {
                                content: '\f067';
                            }
                        }
                    }

                    .menu__button__text {
                        @include screenreader-only();
                    }

                    &[aria-expanded='true'] {
                        .menu__button__icon:before {
                            border-bottom: $border-width-hairline
                                $border-style-solid $color-inline-link;

                            @include media('<=between') {
                                border-bottom: none;
                                content: '\f068';
                            }
                        }

                        &:hover,
                        &:focus {
                            .menu__button__icon:before {
                                border-bottom-color: $color-text;
                            }
                        }
                    }
                }
            }

            .menu__columns {
                @include smalltype();
                background-color: $color-bg;
                display: grid;
                font-weight: $font-weight-text;
                gap: $measure--basic;
                grid-template-columns: repeat(4, 1fr);
                grid-template-rows: auto;
                left: 0;
                position: absolute;
                top: $measure--large1;
                width: 100%;
                z-index: 2;

                @include media('<=large') {
                    padding-left: $measure--basic;
                    padding-right: $measure--basic;
                }

                @include media('<=between') {
                    @include font-family('default');
                    @include deltatype();
                    grid-template-columns: 1fr;

                    .menu__level3 {
                        @include gammatype();
                    }

                    .menu__level4 {
                        @include betatype();
                    }

                    .menu__level5 {
                        @include alphatype();
                    }
                }
            }

            .menu__columns__col + .menu__columns__col {
                @include media('<=between') {
                    display: none;
                }
            }
        }
    }

    .menu__veil {
        background-color: rgba(30, 30, 30, 0.9);
        display: none;
        height: 100%;
        position: absolute;
        width: 100%;
        z-index: 1;

        &[data-on='true'] {
            display: block;
        }
    }
}
