@import 'linked-heading';

.anchor-inline--gray {
    @include anchor-inline--gray;
}

.anchor-inline--gray4 {
    @include anchor-inline--gray4;
}

.anchor-discreet {
    @include anchor-discreet;
}

.anchor-inline--notvisited {
    @include anchor-inline--notvisited;
}

.anchor-inline--dark {
    @include anchor-inline--dark;
}

.anchor-cta-like {
    @include cta-like-anchors;
}

.anchor-underline-transition {
    @include underline-transition;
}

ul.cta-like-anchor-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: none;
    margin: $measure--basic 0 0;

    li.cta-like-anchor {
        width: calc(1 / 2 * 100% - (1 - 1 / 2) * #{$measure--half});
        list-style-type: none;

        @include media('<tablet') {
            width: 100%;
        }

        a {
            @include cta-like-anchors;
        }
    }
}
